<template>
    <div style="height: 100%;">
        <PanelTitle v-if="!hideTitle" :expanded="expanded" :title="title" @expand="onExpandClicked"></PanelTitle>
        <div class="panelContent" v-show="expanded" style="height: calc(100% - 25px); overflow: auto;">
            <slot></slot>
        </div>
    </div>
</template>

<script setup>
import { ref, defineProps, defineEmits } from 'vue';
import PanelTitle from './panels/PanelTitle.vue';

const props = defineProps(['title', 'hideTitle']);
const emit = defineEmits(['update:expanded']);
let expanded = ref(true);
loadExpandedState();

function onExpandClicked() {
    expand( !expanded.value );
}

function expand( yesNo ){
    expanded.value = yesNo;
    saveExpandedState();
    emit('update:expanded', yesNo);
}

function saveExpandedState() {
    localStorage.setItem('panelExpandedState_' + props.title, expanded.value);
}

function loadExpandedState() {
    let state = localStorage.getItem('panelExpandedState_' + props.title);
    if (state === 'false') {
        expanded.value = false;
    }
}

defineExpose({ expanded, expand });
</script>

<style lang="scss" scoped>
/* Your existing styles */
.panelContent{
    padding: 10px;
}
</style>
