<script setup>
  import { ref, onMounted, reactive } from 'vue';
  const props = defineProps([ 'customClass' ]);
  const emit = defineEmits(['changeTab'])
  
  let tabContainer = ref(null);
  let tabHeaders = ref(null);
  let tabs = ref(null);
  let activeTabIndex = ref(0);

  onMounted(() => {
    tabs.value = [ ...tabContainer.value.querySelectorAll('.tab') ];
		for(let x of tabs.value) {
	    if(x.classList.contains('active')) {
				activeTabIndex = tabs.value.indexOf(x);
			}
    }
  })
  const changeTab = (index) => {
    activeTabIndex = index;
    for(let x of [...tabs.value, ...tabHeaders.value]) {
   		x.classList.remove('active')
    }
		tabs.value[activeTabIndex].classList.add('active')  
		tabHeaders.value[activeTabIndex].classList.add('active')  
    emit('changeTab', activeTabIndex);
  }
</script>

<template>
  <div id="tabs-container" :class="customClass" ref="tabContainer" style="height: 100%; display: flex; flex-direction: column;">
    <div id="tab-headers">
      <ul>
        <li v-for="(tab, index) in tabs" :key="index" :class="activeTabIndex == index ? 'active' : ''" @click="changeTab(index)" ref="tabHeaders">{{ tab.title }}</li>
      </ul>
    </div>
    <div id="active-tab" style="flex-grow: 1; display: flex; height: calc(100% - 120px);">
    	<slot></slot>
    </div>
  </div>
</template>

<style>
  #tab-headers ul {
    margin: 0;
    padding: 0;
    display: flex;
    border-bottom: 2px solid #dddddd00;
    margin-bottom: 10px;
  }
  #tab-headers ul li {
    list-style: none;
    padding: 5px 2px;
    position: relative;
    cursor: pointer;
    opacity: 0.5;
    margin: 0px 2px;
  }


  #tab-headers ul li.active {
    /* color: #000000;
    font-weight: bold; */
    opacity: 1;
  }
  
  #tab-headers ul li.active:after {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 0;
    height: 2px;
    width: 100%;
    background: var(--foreground);
  }

  #active-tab, #tab-headers {
    width: 100%;
  }
  
  #active-tab {
    /* padding: 0.75rem; */
  }
</style>