export default class Configuration{
    constructor(){
        throw new Error('Configuration is a static class and cannot be instantiated');
    }


    static appid = '';
    static currentPageIndex = 0;
    static appport = '';
    static CHANNEL__ = '';

    static GB_SOURCE_LOCAL = 'local';
    static GB_SOURCE_CLOUD = 'cloud';

    static GB_SOURCE = '';
    static GB_HOST = '';
    static GB_PORT = '';

    static ask_ai_url = 'https://aiapis.hosting.genieframework.com/api/';
    static TERMS_OF_USE_URL = 'https://genielicensing.hosting.genieframework.com/documents/terms.pdf';
    

    static IS_PRO = false;
    static LICENSE_TYPE = "Unknown";

    static getExternalApiBaseUrl( protocol ){
        if( !protocol ){
            throw new Error( "getExternalApiBaseUrl() requires a protocol argument" );
        }
        // Use secure protocols for cloud environment
        if( Configuration.GB_SOURCE != Configuration.GB_SOURCE_LOCAL ){
            protocol += 's';
        }
        let result = `${protocol}://${Configuration.GB_HOST}`;
        if( Configuration.GB_SOURCE == Configuration.GB_SOURCE_LOCAL ){
            result += `:`;
        }else{
            result += `/proxy/`;
        }
        result += `${Configuration.GB_PORT}/`;
        return result;
    }
    
    static getAppBaseUrl( protocol, appPort ){
        if( !protocol ){
            throw new Error( "getAppBaseUrl() requires a protocol argument" );
        }
        // Use secure protocols for cloud environment
        if( Configuration.GB_SOURCE != Configuration.GB_SOURCE_LOCAL ){
            protocol += 's';
        }
        let result = `${protocol}://${Configuration.GB_HOST}`;
        if( Configuration.GB_SOURCE == Configuration.GB_SOURCE_LOCAL ){
            result += `:`;
        }else{
            result += `/proxy/`;
        }
        result += `${appPort}/`;
        return result;
    }

    static getAppAssetsBaseUrl( protocol, appPort ){
        if( !protocol ){
            throw new Error( "getAppAssetsBaseUrl() requires a protocol argument" );
        }
        // Use secure protocols for cloud environment
        if( Configuration.GB_SOURCE != Configuration.GB_SOURCE_LOCAL ){
            protocol += 's';
        }
        let result = `${protocol}://${Configuration.GB_HOST}`;
        if( Configuration.GB_SOURCE == Configuration.GB_SOURCE_LOCAL ){
            result += `:`;
            result += `${appPort}/`;
        }
        return result;
    }


}