// ===============================================================================
// Import 3rd party modules
// ===============================================================================
import { createApp } from "vue";
import Quasar from 'quasar/src/vue-plugin.js';;
import Toast from "vue-toastification";
import "vue-toastification/dist/index.css";
import quasarUserOptions from "./quasar-user-options";
import axios from "axios";

// ===============================================================================
// Import local modules
// ===============================================================================
import App from "./App.vue";
import store from "./store";
import { loadBlockDefinitions } from "./utils/blockDefinitions";
import Configuration from "./utils/Configuration";

window.axios = axios;
// ===============================================================================
// Global variables and functions
// ===============================================================================
window.currentThemeType = "light";
window.canvasComponents = {};


window.addCanvasComponent = function (id, component) {
  window.canvasComponents[id] = component;
};


window.removeCanvasComponent = function (id) {
  delete window.canvasComponents[id];
};


window.setTheme = function (theme) {
  document.querySelector("body").setAttribute("style", "");
  if (theme === "dark") {
    document.documentElement.setAttribute("data-theme", "dark");
  } else {
    document.documentElement.setAttribute("data-theme", "light");
  }
  window.currentThemeType = theme;
  if (window.switchCodeEditorTheme) {
    window.switchCodeEditorTheme(theme);
  }
};


window.getSearchParams = () => {
  const params = new URLSearchParams(window.location.search);
  const obj = {};
  for (const [key, value] of params.entries()) {
    obj[key] = value;
  }
  return obj;
};


window.addEventListener(
  "message",
  (event) => {
    if (event.data.command === "removeCanvasComponent") {
      window.removeCanvasComponent(event.data.uid);
    } else if (event.data.command === "updateTheme") {
      window.setTheme(event.data.themeType);
    }
  },
  false
);



// Custom-components-related functions
// --------------------------------------------------------------------------------
window.customComponentsCssPaths = [];
window.extractCustomComponentsFromPageAssets = function (componentsList) {
  let appPath = Configuration.getAppBaseUrl('http', Configuration.appport);
  let customComponents = [];
  for( let componentName in componentsList ){
    let componentAssets = componentsList[componentName];
    componentAssets.forEach((asset, index) => {
      // remove prefix slash if present
      if (asset.startsWith("/")) {
        asset = asset.substring(1);
      }
      // PArse each asset according to its type
      if ( asset.endsWith("definitions.json") ){
        customComponents.push( asset );
      }else if ( asset.endsWith(".css") ){
        let cssFullPath = appPath + asset;
        customComponentsCssPaths.push( cssFullPath );
      }
    });
  }
  return customComponents;
};


function loadMultipleUrls(urls, successCallback, completeCallback) {
  const requests = urls.map((url) => fetch(url).then(response => {
    if (!response.ok) {
      throw new Error(`Failed to fetch ${url}: ${response.statusText}`);
    }
    return response.json();
  }));

  Promise.all(requests)
    .then((responses) => {
      responses.forEach((response) => {
        // Execute the success callback for each response
        successCallback(response);
      });
    })
    .catch(error => {
      console.error('One or more requests failed:', error);
    })
    .finally(() => {
      // Execute the complete callback when all requests have been loaded
      completeCallback();
    });
}

// UX/UI event logging
window.logUserEvent = ( name, data )=>{
  if( name == undefined || data == undefined ){
    return;
  }
  //console.log(`logUserEvent: ${name}`, data);
  window.parent.postMessage({
    command: "logUserEvent",
    name: name,
    data: data
  }, 
  "*");
}

// MAIN FUNCTION (entrypoint)
// --------------------------------------------------------------------------------

function startNocodeApplication() {
  // ==============================================================
  //                      URL PARAMS
  // ==============================================================

  // Get the URL params to configure connectivity with backend
  // Check url params and save in Configuration if all good
  let expectedParamsList = [
    "appid",
    "appport",
    "CHANNEL__",
    "GB_SOURCE",
    "GB_HOST",
    "GB_PORT",
    "USER_EMAIL",
    "SESSIONID",
  ];
  let urlParamsMissing = [];
  window.searchParams = getSearchParams();
  let report = "NCD. URL Params\n------------------------";
  expectedParamsList.forEach((paramName) => {
    report += `\n  - ${paramName}: ${searchParams[paramName]}`;
    if (searchParams[paramName] == undefined) {
      urlParamsMissing.push(paramName);
    } else {
      Configuration[paramName] = searchParams[paramName];
    }
  });
  Configuration.elementid = searchParams.elementid;

  // Throw error if any param is missing
  if (urlParamsMissing.length > 0) {
    let urlParamsMessage = "Missing URL params: " + urlParamsMissing.join(", ");
    alert(urlParamsMessage);
    throw new Error(urlParamsMessage);
  }

  // Set session id header for all requests
  axios.defaults.headers.common["gb-sessionid"] = `${Configuration.SESSIONID}`;

  // Build API base (used for ajax requests against the GB API)
  window.apibase = Configuration.getExternalApiBaseUrl("http") + "api/v1/";

  // Set the UI Theme
  window.themeType = "dark";
  if (searchParams.theme == "light") {
    themeType = "light";
  }
  setTheme(themeType);

  // To-do: the "pro" setting should be set by the backend, we need to implement this properly
  // here's just a quick implementation as a POC
  Configuration.IS_PRO = searchParams.ispro == "true";
  Configuration.LICENSE_TYPE = searchParams.license_type;
  Configuration.currentPageIndex = searchParams.pageindex;

  if (!apibase || !Configuration.appid) {
    return;
  }

  // Load the list of apps
  axios
    .get(apibase + "apps")
    .then((result) => {
      // Find the requested app
      let myApp = result.data.applications.find((app) => {
        return app.id.value.toString() == Configuration.appid;
      });
      window.fullAppData = myApp;

      if (myApp != null) {
        // Get app's current page info
        axios
          .get(apibase + "apps/" + Configuration.appid + "/pages")
          .then((result) => {
            let currentPage = result.data.pages[0];
            // Get custom Component definitions paths from the page assets
            let customComponents = extractCustomComponentsFromPageAssets(
              currentPage.gb_components
            );
            // Build the final URLs to load the custom components
            let appPath = Configuration.getAppBaseUrl('http', Configuration.appport);
            customComponents = customComponents.map((customComponent) => {
              // remove prefix slash if present
              if (customComponent.startsWith("/")) {
                customComponent = customComponent.substring(1);
              }
              let fullPath = appPath + customComponent;

              return fullPath;
            });

            let customComponentsDefinitions = [];
            loadMultipleUrls(
              customComponents,
              // for each loaded url
              (customComponentDef) => {
                let customBlockDefJsonObject = customComponentDef;
                let CP = currentPage;
                let gbPluginId = customBlockDefJsonObject.gbPluginId;
                let pluginAssets = CP.gb_components[gbPluginId];
                let iconRelativePath = pluginAssets.find( asset =>{
                    return asset.endsWith("png");
                });
                if( iconRelativePath ){
                  let iconAbsolutePath = "{appPath}" + iconRelativePath;
                  customBlockDefJsonObject.icon = iconAbsolutePath;
                }
                
                customComponentsDefinitions.push(customBlockDefJsonObject);
              },
              // on all urls loaded (on-comple callbak)
              () => {
                loadBlockDefinitions((definitions) => {
                  window.blockDefinitions = definitions;

                  // Add custom components to the list
                  customComponentsDefinitions.forEach((customComponentDef) => {
                    window.blockDefinitions.push(customComponentDef);
                  });

                  // Create and mount the Vue app
                  let app = createApp(App);
                  const toastOptions = {
                    position: "top-right",
                    timeout: 50000,
                    closeOnClick: true,
                    pauseOnFocusLoss: true,
                    pauseOnHover: true,
                    draggable: false,
                    //draggablePercent: 0.6,
                    showCloseButtonOnHover: true,
                    hideProgressBar: true,
                    closeButton: "button",
                    icon: true,
                    rtl: false,
                  };
                  app.use(Toast, toastOptions);
                  app.use(Quasar, quasarUserOptions).use(store).mount("#app");
                });
              }
            );
          })
          .catch((error) => {
            console.log("error", error);
          });
      }
    })
    .catch((error) => {
      console.log("error", error);
    });
}


// Call entrypoint
startNocodeApplication();
