<template>
    <div>
        <vscode-text-field 
            :value="searchTerm"
            @input="(event)=>searchTermChanged(event)" placeholder="Search UI Components" style="padding: 5px 10px; width: 90%;"></vscode-text-field>
    </div>
    <div style="max-height: 100%; overflow-y: auto; margin-top: 10px; user-select: none; flex-grow: 1;">
        <div style="display: flex; flex-wrap: wrap; gap: 5px;flex-direction: column;">
            <div v-for="category in blocksByCategory" :key="category.category" style="display: flex; flex-direction: column; width: 100%;">
                <div style="font-weight: bold; margin-bottom: 5px; text-transform: capitalize; margin-top: 10px;">{{ category.category }}</div>
                <div class="uiItemContainer">
                    <div v-for="block in category.blocks" :key="block.id" 
                        :blocktype="block.type"
                        :class="{uiItem:true, uiItemProDisabled: block.proRequired && !isPro}" 
                        @mousedown="(!block.proRequired) || ( block.proRequired  && isPro ) ? dragStart(block) : showUpgradeLicenseDialog(true)"
                        :title="block.proRequired && !isPro ? 'Upgrade to PRO plan to use this component and many more!' : block.label"
                        >
                            <div :class="{ uiItemLabel:true, uiItemLabelProRequired:block.proRequired && !isPro }">{{ block.label }}</div>
                            <div :class="{ uiItemIcon:true, uiItemIconProRequired:block.proRequired && !isPro }">
                                <img :src="getBlockIconPath(block)" class="libraryIcon" style=""/>
                            </div>
                            <img v-if="block.proRequired && !isPro" src="img/icons/pro_badge.png" class="proBadge" style=""/>
                    </div>
                    <div class="disabledProPanel"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>

//import blockDefinitions from '../../utils/blockDefinitions'
import { ref,computed, onMounted } from 'vue'
import Configuration from '../../utils/Configuration';
import _ from 'lodash';

const isPro = Configuration.IS_PRO;

function showUpgradeLicenseDialog(showDialog=true){
    window.showUpgradeLicenseDialog(showDialog);
}

// Exclude some block types from the list
const excludedTypes = ["tempplaceholder", "partial"];
let blockDefinitionsRaw = blockDefinitions.filter( block => {
    return excludedTypes.indexOf( block.type ) === -1;
} );

//debugger;
// HTML blocks
// =============================================
blockDefinitionsRaw.push( { description: "", draggable: true, droppable: true, editable: true, enabled: true, label: "1-Col Row", properties:[], tagName: "DIV", type: '1column', content: `<div class="row">
<div class="st-col col-12 col-sm st-module"></div></div>`, media: ``, category: 'Layout'   }); 

blockDefinitionsRaw.push( { description: "", draggable: true, droppable: true, editable: true, enabled: true, label: "2-Col Row", properties:[], tagName: "DIV", type: '2columns', content: `<div class="row">
<div class="st-col col-12 col-sm st-module"></div>
<div class="st-col col-12 col-sm st-module"></div>
</div>`, media: ``, category: 'Layout'   }); 


blockDefinitionsRaw.push( { description: "", draggable: true, droppable: true, editable: true, enabled: true, label: "3-Col Row", properties:[], tagName: "DIV", type: '3columns', content: `<div class="row">
<div class="st-col col-12 col-sm st-module"></div>
<div class="st-col col-12 col-sm st-module"></div>
<div class="st-col col-12 col-sm st-module"></div>
</div>`, media: ``, category: 'Layout'   }); 

blockDefinitionsRaw.push( { description: "", draggable: true, droppable: true, editable: true, enabled: true, label: "Column", properties:[], tagName: "DIV", type: '1column', content: `<div class="st-col col-12 col-sm st-module"></div>`, media: ``, category: 'Layout'   }); 


blockDefinitionsRaw.push( { description: "", draggable: true, droppable: true, editable: true, enabled: true, label: "Sidebar Left", properties:[], tagName: "DIV", type: 'sidebar_left', content: `<header class="st-header q-pa-sm">
<h1 class="st-header__title text-h3">Header Text</h1>
</header>
<div class="row">
<div class="st-col col-3 col-sm st-module"></div>
<div class="st-col col-9 st-module"></div>
</div>
<footer class="st-module">Page footer</footer>
`, media: ``, category: 'Layout'   }); 

blockDefinitionsRaw.push( { description: "", draggable: true, droppable: true, editable: true, enabled: true, label: "Sidebar Right", properties:[], tagName: "DIV", type: 'sidebar_right', content: `<header class="st-header q-pa-sm">
<h1 class="st-header__title text-h3">Header Text</h1>
</header>
<div class="row">
<div class="st-col col-9 st-module"></div>
<div class="st-col col-3 col-sm st-module"></div>
</div>
<footer class="st-module">Page footer</footer>
`, media: ``, category: 'Layout'   }); 

blockDefinitionsRaw.push( { description: "", draggable: true, droppable: true, editable: true, enabled: true, label: "Sidebars", properties:[], tagName: "DIV", type: 'sidebars',  content: `<header class="st-header q-pa-sm">
<h1 class="st-header__title text-h3">Header Text</h1>
</header>
<div class="row">
<div class="st-col col-3 col-sm st-module"></div>
<div class="st-col col-5 st-module"></div>
<div class="st-col col-3 col-sm st-module"></div>
</div>
<footer class="st-module">Page footer</footer>
`, media: ``, category: 'Layout'   });  

blockDefinitionsRaw.push( {
    label: "Header", tagName: "header", type: 'header', description: "", category: 'Layout', media: ``,
    draggable: true, droppable: true, editable: true, enabled: true, properties:[],
    content: `<header ><h1>Page header</h1></header>`
});

blockDefinitionsRaw.push( { 
    label: "Footer", tagName: "footer", type: 'footer', description: "", category: 'Layout', media: ``, 
    draggable: true, droppable: true, editable: true, enabled: true, properties:[],   
    content: `<footer class="st-module">Page footer</footer>`    
});  





// =============================================
// End HTML blocks

let categoriesSorting = [
    "Layout", 
    "Text", 
    "Content", 
    "Forms", 
    "Widgets", 
    "Lists", 
    "Tables", 
    "Charts", 
    "Multimedia", 
    "Timeline", 
    "Other", 
];

let blocksByCategory = ref(null);
let searchTerm = ref('');

// Variables needed for the custom implementation of drag-and-drop
let isDragging = false;
let draggableElement = null;
let iframeB = null;

let blocks = blockDefinitions.map( (item)=>{
  let newDef = {
    type: item.type,
    label: item.label, 
    tagName: item.tagName,

    id: item.type,
    category: item.category, 
    content: item.content,
    droppable: item.droppable,
    draggable: item.draggable,
    editable: item.editable,

    //media: item.media, 
    activate: true, 
  }
  return newDef;
});

let dragStart, dragStop;

function handleBlocks(props) {
      blocks = props.blocks;
      dragStart = (block)=>{
        window.logUserEvent("library::dragToCanvas", { component: block.type });
        props.dragStart(block)
      };
      dragStop = props.dragStop;
      emit("blockCustomTriggered", props );
    };

let editorCheckInterval;

onMounted( ()=>{
    editorCheckInterval = setInterval( ()=>{
        if( window.editor ){
            clearInterval( editorCheckInterval );
            window.editor.on('block:custom', handleBlocks);
            const { Panels } = window.editor;
            Panels.getButton('views', 'open-blocks').set('active', true);
        }else{
            //console.log( "[debug] LibraryPanel :: onMounted :: window.editor NOT ready" );
        }
    }, 1000);
});


function searchTermChanged(event){
    debouncedLogSearch();
    buildCategoriesTree(event);
}

function buildCategoriesTree(event){
    //console.log( 'buildCategoriesTree :: newValue: ', event );
    if( event != undefined )
        searchTerm.value = event.target.value;
    
    let categories = [];
    let categoriesDict = {};
    let blocks = [];
    filteredBlockDefinitions.value.forEach( block => {
        if( block.hideFromLibrary == true ) return;
        let category = block.category || 'other';
        if( !categoriesDict[ category ] ){
            categoriesDict[ category ] = { category: category, blocks: [] }
            categories.push( categoriesDict[ category ] );
        }
        categoriesDict[ category ].blocks.push( block );
    });
    // Sort categories based on categoriesSorting sorting array
    categories.sort( (a,b)=>{
        let aIndex = categoriesSorting.indexOf( a.category );
        let bIndex = categoriesSorting.indexOf( b.category );
        if( aIndex > bIndex ) return 1;
        if( aIndex < bIndex ) return -1;
        return 0;
    });

    // within each category, sort blocks by label
    categories.forEach( category => {
        category.blocks.sort( (a,b)=>{
            if( a.label > b.label ) return 1;
            if( a.label < b.label ) return -1;
            return 0;
        });
    });

    blocksByCategory.value = categories;
    logCategories();
    return categories;
}

function logCategories(){
    let output = "Block Categories:\n--------------------\n";
    blocksByCategory.value.forEach( category => {
        output += category.category + "\n";
        category.blocks.forEach( block => {
            output += "  " + block.label + "\n";
        });
    });
    //console.log( output );
}

const filteredBlockDefinitions = computed( ()=>{
    if( !searchTerm.value ) return blockDefinitionsRaw;
    let results = blockDefinitionsRaw.filter( block => {
        return block.label.toLowerCase().indexOf( searchTerm.value.toLowerCase() ) > -1;
    });
    return results;
});

buildCategoriesTree();

const getBlockIconPath = function(block)  {   
    let iconFileName;
    let result;
    // If icon is explicitly defined, use it
    if( block.icon ){
        // If icon starts with {appPath}, replace this token with the actual app path
        if( block.icon.startsWith("{appPath}") ){
            let appPath = Configuration.getAppBaseUrl('http', Configuration.appport);
            // remove trailing slash if present
            if( appPath.endsWith("/") ) appPath = appPath.slice(0, -1);
            iconFileName = block.icon.replace("{appPath}", "");
            result = appPath + iconFileName;
        }else{
            // Else just use it as is
            iconFileName = block.icon;
            result = 'img/block_icons/'+iconFileName
        }
    // If icon is not defined, use the block type and resolve the icon file name from it
    }else{
        iconFileName = block.type.split("q-").join("")+'.png';
        result = 'img/block_icons/'+iconFileName
    }
    return result;
}


function onDragStart(block, $event) {
    iframeB = document.querySelector(".gjs-frame");

    sorter = editor.BlockManager.blocksView.getSorter();
    sorter.__currentBlock = block;
    sorter.setDragHelper( $event.target, $event);
    sorter.setDropContent(block.content);
    sorter.startSort($event.target);

    //sorter = editor.Utils.getSorter();

    // Traverse up the DOM tree to find the .uiItem element
    let target = $event.target;
    while (target && !target.classList.contains('uiItem')) {
        target = target.parentElement;
    }

    if (target) {
        isDragging = true;
        draggableElement = target;
        target.setPointerCapture($event.pointerId);
    }
}

function onPointerMove($event){
    if (!isDragging || !sorter) return;

    const rect = iframeB.getBoundingClientRect();
    const x = $event.clientX - rect.left;
    const y = $event.clientY - rect.top;
}


function onPointerUp(block, event){
    if (sorter) {
        sorter.endMove();
        sorter = null;
    }

    isDragging = false;
    const rect = iframeB.getBoundingClientRect();
    if (
        event.clientX >= rect.left &&
        event.clientX <= rect.right &&
        event.clientY >= rect.top &&
        event.clientY <= rect.bottom
    ) {
        draggableElement.style.pointerEvents = 'none';  // Temporarily disable pointer events for the dragged element

        const iframeDoc = iframeB.contentDocument;
        const x = event.clientX - rect.left;
        const y = event.clientY - rect.top;
        const elementBelow = iframeDoc.elementFromPoint(x, y);

        let elementBelowId = elementBelow.getAttribute('id');
        draggableElement.style.pointerEvents = '';  // Restore pointer events for the dragged element
        draggableElement.style.position = 'static';

        // Get the GrapesJS component corresponding to elementBelow        
        let targetComponent = editor.DomComponents.getWrapper().find('#'+elementBelowId)[0];

        if (targetComponent) {
            targetComponent.components().add( block.content );
            // Refresh the canvas to reflect the changes
            editor.refresh();
        }
    } else {
        draggableElement.style.position = 'static';
    }
    draggableElement = null;
}

let debouncedLogSearch = _.debounce(function () {
    if( searchTerm.value.length > 0 ){
        window.logUserEvent("library::search", { searchTerm: searchTerm.value });
    }
  }, 1000);

const emit = defineEmits(['blockCustomTriggered'])
</script>



