<template>
    <div class="bindingsConnectorsContainer">
            <svg width="100%" height="100%" style="position: absolute; z-index: 11; pointer-events: none;">
                <rect v-for="connection in connections" :key="connection.id" :x="connection.x" :y="connection.y" :width="connection.width" :height="connection.height" style="fill:transparent; stroke-width:3; stroke:#0099ff;"/>
                <path  v-for="connection in connections" :key="connection.id" 
                    :d="getConnectionSVGPath(connection)" stroke="#0099ff" stroke-width="2" fill="transparent"/>
            </svg>
    </div>
</template>

<script setup>
import { ref } from 'vue'


const canvasElementsPositions = ref([]);
const connections = ref([]);
const _iframePosition = ref(null);
const overedBinding = ref(null);


function getConnectionSVGPath(connection ){
    let origin = connection;
    let target = getOveredBindingPosition();
    let xDistance = Math.abs( origin.x+origin.width - target.x );
    let controlPointOffsetX = xDistance*0.5;
    let path = `M ${origin.x+origin.width} ${origin.y+origin.height*.5} C ${origin.x+origin.width+controlPointOffsetX} ${origin.y+origin.height*.5}, ${target.x-controlPointOffsetX} ${target.y+7}, ${target.x} ${target.y+7}`;
    return path;
}



function getOveredBindingPosition(){
    let elPosition = getElementPosition( overedBinding.value.element );
    return elPosition;
}



function getElementPosition(element) {
  const rect = element.getBoundingClientRect();
  return {
    x: rect.left, 
    y: rect.top
  };
}

function updateElementsPositions(lines){
    canvasElementsPositions.value = lines;
    updateConnections();

}
function updateConnections(){
    if( _iframePosition.value == null )
        updateIframePosition();
    let iframePosition = _iframePosition.value;
    let adjustedPositions = canvasElementsPositions.value.map( (line)=>{
        return {
            x: line.x + iframePosition.x,
            y: line.y + iframePosition.y, 
            width: line.width,
            height: line.height,
        }
    })
    connections.value = adjustedPositions;

}

function updateIframePosition(){
    let iframeElement = document.querySelector('.gjs-frame') ;
    _iframePosition.value = getElementPosition( iframeElement );;
    updateConnections();
}

function onBindingOvered(event){
    overedBinding.value = event;
}



defineExpose({
    updateElementsPositions, updateIframePosition, onBindingOvered
})
</script>

<style scoped>
.bindingsConnectorsContainer{
    background-color: #ff000000;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 10;
    pointer-events: none;
}
</style>