<template>
    <div style="position:relative;">
        <div style="overflow-y: auto;">
            <div 
                :title="block.binding.type"
                isbinding="true" 
                @mouseover="onBindingOvered(block.binding, $event)" @mouseout="onBindingOut"
                @mousedown="dragStart(block)" 
                :datatype="block.binding.type" 
                :bindingname="block.binding.name" 
                class="bindingItem"
                v-for="block in sortedBindingsAsBlocks" :key="block.binding.name">
                    <img :datatype="block.binding.type" 
                        :bindingname="block.binding.name" :src="'img/icons/type_'+block.binding.type+'.png'" class="typeIcon" />
                    {{ block.binding.name }} 
            </div>
        </div>
        <!-- <div class="sortButtons">
            <div :class="{sortButton: true, sortButtonActive: sortMethod=='az'}" @click="sortMethod='az'">AZ</div>
            <div :class="{sortButton: true, sortButtonActive: sortMethod=='type'}" @click="sortMethod='type'">Ty</div>
        </div> -->
    </div>
</template>

<script setup>

import _ from 'underscore';
import {computed, ref, onMounted} from 'vue';
import Configuration from '../../utils/Configuration';

let sortMethod = ref('type');

const props = defineProps(['appData']);
const emit = defineEmits(['bindingOvered'])

let dragStart, dragStop;


function setHandleBlocks(props) {
      dragStart = props.dragStart;
      dragStop = props.dragStop;
};


const sortedBindingsAsBlocks = computed( ()=>{
    let blocks = [];
    sortedBindings.value.forEach( binding => {
        blocks.push( 
            {   id: "tempplaceholder", 
                type: "tempplaceholder", label: "Temp PlaceHolder", tagName: "TEMPPLACEHOLDER", 
                content: `<tempplaceholder datatype="${binding.type}" bindingname="${binding.name}" isbinding="true"style="padding: 10px;">Binding '${binding.name}' (${binding.type})</tempplaceholder>`, 
                properties: [], 
                droppable: false,     editable: false,     draggable: true,
                binding: binding,
            }
        );
    });
    return blocks;
});


const sortedBindings = computed( ()=>{
    let sorted;
    if( props.appData.pages[Configuration.currentPageIndex].model.fields ){
        if( sortMethod.value == 'az' )
            sorted = _.sortBy( props.appData.pages[Configuration.currentPageIndex].model.fields, (field)=>{ return field.name.toLowerCase() } );
        else if( sortMethod.value == 'type')
            sorted = _.sortBy( props.appData.pages[Configuration.currentPageIndex].model.fields, (field)=>{ return field.type.toLowerCase() + "_" + field.name.toLowerCase() } );
    }
    return sorted;
});


const onBindingOvered = function(binding, $event){
    let element = $event.target;
    emit('bindingOvered', { element, binding } );
}


const onBindingOut = function(){
    emit('bindingOvered', { element:null, binding:null } );
}

defineExpose({
    setHandleBlocks
}) 

</script>

<style lang="scss" scoped>
@import "../../styles/globalStyles.scss";

.typeIcon{
    width: 16px;
    vertical-align: middle;
    margin-right: 6px;
    height: 16px;
}


.bindingItem{
    padding: 1px 0px 2px;
    border-bottom: solid 1px rgba(0, 0, 0, 0.1254901961);
    font-size: 0.9em;
    display: flex;
    user-select: none;
    cursor: grab;
}

.bindingItem:hover{
    background-color: #cccccc10;
}

.sortButton{
    margin-left: 3px;
    font-weight: bold;
    font-size: 10px;
    padding: 5px;
    user-select: none;
    cursor: pointer;
    opacity: 0.4;
    border-radius: 5px;;
}

.sortButton:hover, .sortButtonActive{
    background-color: #00000010;
    opacity: 1;
}
.sortButtons{
    position: absolute;
    right: 2px;
    top: -40px;    
    display: flex;
    flex-direction: row;
}

</style>