function applyCodeChanges(){
  let fullContent = codeEditorValue.value;
  const parser = new DOMParser();
  const doc = parser.parseFromString(fullContent, 'text/html');
  const firstDiv = doc.body;
  HtmlTemplateParser.encodeAttributesInDOM(firstDiv );
  
  // Get the styles before swapping the component or they will get lost
  const firstDivContent = firstDiv.innerHTML;
  let previousStyle = editor.getCss();
  editor.getSelected().replaceWith( firstDivContent )
  editor.setStyle(previousStyle);

  window.logUserEvent( "code-editor::appliedChanges", { 
      component: window.selectedComponent.attributes.tagName, 
  });
}


