function translateBindingTypes(pages){
  const typesMap = {
              "{Function}": "Function", 
              "{Bool}": "Boolean", 
              "{String}": "String", 
              "{Char}": "String", 
              "{Date}": "Date", 
              "{Number}": "Number", 
                  "{Int64}": "Number", 
                  "{Int32}": "Number", 
                  "{Int16}": "Number", 
                  "{Int8}": "Number",
                  "{Float64}": "Number",
                  "{Float32}": "Number",
                  "{Float16}": "Number",
                  "{Float8}": "Number",
              "{Vector}": "Array",
                  "{Vector{Any}}": "Array",
                  "{Vector{Bool}}": "Array",
                  "{Vector{String}}": "Array",
                  "{Vector{Char}}": "Array",
                  "{Vector{Number}}": "Array",
                  "{Vector{Float64}}": "Array",
                  "{Vector{Int64}}": "Array",
                  "{Vector{Int32}}": "Array",
                  "{Vector{Int16}}": "Array",
                  "{Vector{Int8}}": "Array", 
                  "Vector{Dict}": "Array", 
              "{Object}": "Object", 
              "{NamedTuple{(:min, :max), Tuple{Int64, Int64}}}": "Object", 
              "{Dict}": "Object", 
              "{RangeData{Int64}}": "Object", 
              "{Vector{PlotlyBase.GenericTrace{Dict{Symbol, Any}}}}": "PlotlyBase.GenericTrace", 
              "{PlotlyBase.Layout{Dict{Symbol, Any}}}": "PlotlyBase.Layout", 
              "{DataTable{DataFrames.DataFrame}}": "DataTable.DataFrames.DataFrame", 
              "{DataFrames.DataFrame}": "DataFrames.DataFrame", 
              "{DataFrame}": "DataFrames.DataFrame", 
              "{PlotlyData}": "PlotlyData", 
              "{DataTablePagination}": "DataTablePagination", 
          };
  pages.forEach( (page)=>{
    // remove non-reactive vars
    page.model.fields = page.model.fields.filter( (field)=>{ return field.isreactive} );
    // translate types
    page.model.fields.forEach( (field)=>{
      let fieldType = field.type;
      let cleanType = fieldType.replace("Reactive", "");
      let mappedType = typesMap[cleanType];
      // Special case for ANY kind of Vector with Dicts inside (array of objects) suitable for Q-Table,
      // And vector of vectors (i.e. array of arrays) suitable for HeatMap Chart's Z axis
      if( mappedType == undefined && (cleanType.indexOf( '{Vector{Dict{') == 0 || cleanType.indexOf( '{Vector{Vector{') == 0 ) ){
        mappedType = "Array";
      }
      if( mappedType ){
        field.type = mappedType;
      }else{
        field.type = "UnknownType";
      }
    } );
    
    // sort alphabetically
    page.model.fields = _.sortBy( page.model.fields, (field)=>{ return field.name.toLowerCase() } );
  } );
}




// GB app's HTML Template parsing (load/save/parse...)
// ===============================================================================
function getCurrentHtmlAndCss(){
  let editedHtmlCss = editor.getCss();
  const entireHtml = editor.getHtml({ cleanId: true });
  const parser = new DOMParser();
  const doc = parser.parseFromString(entireHtml, 'text/html');
  const firstDiv = doc.body.querySelector('#gbTemplateContainer') || null;  

  // cleanup css, as grapes sometimes adds unwanted rules for global and body
  // Regular expression to match '* {...}' and 'body {...}' patterns
  let pattern = /\*\s*{[^}]*}|body\s*{[^}]*}/g;
  // Replace the matched patterns with an empty string
  editedHtmlCss = editedHtmlCss.replace(pattern, '');
  
  
  HtmlTemplateParser.decodeAttributesInDOM(firstDiv );
  
  // On element attributes with value = "true" Grapes will return them empty (as it's the HTML standar)
  // This is breaking when it's a dynamic Vue attribute, since Vue requires a value to be passed
  // So we need to "fix" the HTML before saving it, by enforcing the "true" value to be present
  
  
  const firstDivContent = firstDiv.innerHTML;



    let beautifiedHtml = html_beautify ( firstDivContent, beautifyOptions );

    // Restore special tags
    beautifiedHtml = HtmlTemplateParser.restoreSpecialElements( beautifiedHtml );

    return { beautifiedHtml, editedHtmlCss };
}


function mergeCss(fullCss, partialCss) {
    function parseCss(css) {
        const regex = /([^{]+)\{([^}]+)\}/g;
        let matches;
        const parsed = {};
        
        while ((matches = regex.exec(css)) !== null) {
            const selector = matches[1].trim();
            const properties = matches[2].trim();
            parsed[selector] = properties;
        }
        return parsed;
    }

    function stringifyCss(cssObj) {
        return Object.entries(cssObj).map(([selector, properties]) => {
            return `${selector} { ${properties} }`;
        }).join(' ');
    }

    const fullCssObj = parseCss(fullCss);
    const partialCssObj = parseCss(partialCss);
    for (const [selector, properties] of Object.entries(partialCssObj)) {
        fullCssObj[selector] = properties;
    }
    return stringifyCss(fullCssObj);
}


function getCleanHtmlCode(htmlString){
  let parser = new DOMParser();
  let doc = parser.parseFromString(htmlString, 'text/html');
  const firstDiv = doc.body || null;  
  const firstDivContent = firstDiv.innerHTML;
  let beautifiedHtml = html_beautify ( firstDivContent, beautifyOptions )
  beautifiedHtml = HtmlTemplateParser.restoreSpecialElements( beautifiedHtml );
  return beautifiedHtml; 
}


function onAutoSaveTriggered(){
  saveTemplate(); 
}


function saveTemplate(force){
  if( force ){
    window.logUserEvent( "toolbar::click", { element:"save" } );
  }
  // Get the current HTML and CSS
  let { beautifiedHtml, editedHtmlCss } = getCurrentHtmlAndCss();
  
  // Skip saving if the document contains a temp placeholder
  let containsTempPlaceHolder = beautifiedHtml.indexOf('<tempplaceholder') > -1;
  if( containsTempPlaceHolder ){
    return;
  }

  // Process both new and old (initially loaded) HTML code to properly compare for changes
  let cleanBeautifiedCode = getCleanHtmlCode( beautifiedHtml );
  let cleanLastSavedCode = getCleanHtmlCode( lastSavedHTMLCode );  
  let contentHasChanged = cleanLastSavedCode != cleanBeautifiedCode;
  
  // If the content has not changed, do not save (unless forced to do so, which happens when user manually clicks save)
  if( contentHasChanged || force ){
      lastSavedHTMLCode = beautifiedHtml;
  }else{
    return;
  }

   // ====== Special case when editing inner element ======
   if( Configuration.elementid ){
      const parser = new DOMParser();
      const doc = parser.parseFromString(fullDocumentHTML, 'text/html');
      let element = doc.querySelector( "#"+Configuration.elementid);
      element.innerHTML = beautifiedHtml;

      // process the full document
      const firstDiv = doc.body;
      const firstDivContent = firstDiv.innerHTML;
      beautifiedHtml = html_beautify ( firstDivContent, beautifyOptions );
      beautifiedHtml = HtmlTemplateParser.restoreSpecialElements( beautifiedHtml );
    }
   
   // Prepare path for saving
  let pageViewPath = currentPage.view;
  let encodedViewPath = encodeURIComponent(pageViewPath);
  let saveUrl = `${apibase}apps/${Configuration.appid}/save?path=${encodedViewPath}`;

  // Set flag to prevent reload on file change
   savedFromNoCodeEditor = true;
    axios.post( saveUrl, { payload: beautifiedHtml } )
        .then(  (response) => {
          // Catch server errors
          if( response.status != 200 ){
            showApiErrorAlert( `There was an issue saving your changes (code ${response.status}). If the problem persists please try reloading this window.` );
            return;
          }
          // If editing specific element, do not save css file
          // To-do: rad full css and merge with edited one
          if( Configuration.elementid ){
            let mergedCss = mergeCss( fullDocumentCSS, editedHtmlCss );
            editedHtmlCss = mergedCss;
          }

          // The GB back-end doesn't seem to notify (via ws) all changes when multiple files are saved at once
          // Specifically, tchanges on the html file are not notified if we save the css immediately after.
          // So we need to wait a bit before saving the css file
          // To-do: this is a temporary fix, we need to find a better way to handle this. 
          // GB server should notify changes to all files, not just the last one
          setTimeout( ()=>{
            let saveCssUrl = `${apibase}apps/${Configuration.appid}/save?path=public/css/autogenerated.css`;
            axios.post( saveCssUrl, { payload: editedHtmlCss } )
              .then(  (response) => {
                savedFromNoCodeEditor = false;
              } )
              .catch( (error) => {
              });
          }, 1000 );
          
        } )
        .catch( (error) => {
          // Catch server errors
          showApiErrorAlert( `There was an issue saving your changes (Network error). If the problem persists please try reloading this window.` );
        });
}




// Unsaved changes management
// ===============================================================================
function clearStoredUnsavedChanges(){
  let currentPath = currentPage.config.app_path + '/' + currentPage.view;
  localStorage.removeItem('unsavedChanges__' + currentPath);
}

function getStoredUnsavedChanges(){
  let currentPath = currentPage.config.app_path + '/' + currentPage.view;
  let unsavedChanges = localStorage.getItem('unsavedChanges__' + currentPath);
  if( unsavedChanges ){
    unsavedChanges = JSON.parse(unsavedChanges);
  }
  return unsavedChanges;
}

function setStoredUnsavedChanges (){
  let currentPath = currentPage.config.app_path + '/' + currentPage.view;
  let currentContents = getCurrentHtmlAndCss();
  currentContents = JSON.stringify(currentContents);
  localStorage.setItem('unsavedChanges__' + currentPath, currentContents);
}



