    window.parent.postMessage( {command:"informNoCodeVersion", metadata:{nocodeVersion:version} }, "*" );
}

function showApiErrorAlert(error){
  window.parent.postMessage( {command:"showApiErrorAlert", message:error }, "*" );
}



// GB API Websocket
// ===============================================================================
function onSocketOpen(data){
}

function onSocketMessage(data){
  let messageData = JSON.parse(data.data);
  // decode message if it's base64-encoded
  if( messageData.message && messageData.message.indexOf('base64:') == 0 ){
    messageData.message = atob( messageData.message.replace('base64:', '') );
  }
  // Check if the message is a file change notification and reload the page if it's not the current page
  if( messageData.message && messageData.message.indexOf('filechanged:') == 0 ){
    let filePath = messageData.message.replace('filechanged:', '');
    let currentPageFullPath = currentPage.config.app_path + '/' + currentPage.view;
    // To-do: probably need to change appId too, if it is received within the socket message
    let isSameFile = ( filePath == currentPageFullPath );
    let isAutogeneratedCss = ( filePath.indexOf('autogenerated.css' ) > -1 );
    let isThemeCss = ( filePath.indexOf('theme.css' ) > -1 );
    
    if( isThemeCss || (!isAutogeneratedCss && !savedFromNoCodeEditor) ){
      if( !isSameFile ){
        setStoredUnsavedChanges();
      }
      window.location.reload();
    }
  }else if( logsPanel&& logsPanel.value){
    logsPanel.value.addLog( { appid: messageData.appid, timestamp: messageData.timestamp, source: 'server', level: messageData.type, message: messageData.message + ': ' + messageData.status } );
  }
}

function onSocketClose(data){
  statusMonitor.restart(5); // Attempt to restart with some delay
}



// External methods (called from other components or contexts)
// ===============================================================================
function startEditingComponentContent(){
  const selectedElement = editor.getSelected();
  window.logUserEvent("component::editNested", { component: selectedElement.attributes.tagName });
  window.parent.postMessage( { 
    command:"editNestedComponent", 
    appid:searchParams.appid, 
    filepath:searchParams.filepath, 
    ccid: selectedElement.getId()
  }, "*" );
}

defineExpose({
  startEditingComponentContent
})





// Main function (Entry point)
// ===============================================================================
function init(){
  informNoCodeVersion();

  window.showUpgradeLicenseDialog = ( showDialog=true )=>{
    window.parent.postMessage( {command:"showUpgradeLicenseDialog", metadata:{ showDialog } }, "*" );
  }

  window.canvasPanel = canvasPanel;
  // listen for scrolling to update iframe position
  document.addEventListener('scroll', e => {
    let bindConnMgr = bindingConnectorsManager.value;
    if( bindConnMgr == null )
      return;
    bindConnMgr.updateIframePosition();
  });

  // Listen to common shortcuts (undo/redo/copy/paste...)

  document.addEventListener('keydown', e => {
    if( e.ctrlKey || e.metaKey ){
      switch( e.key ){
        case "s":
          e.preventDefault();
          saveTemplate();
          break;
        case "z":
          e.preventDefault();
          if( e.shiftKey)
            document.execCommand("redo");
          else
            document.execCommand("undo");
          break;
        case "c":
          e.preventDefault();
          document.execCommand("copy");
          break;
        case "v":
          e.preventDefault();
          document.execCommand("paste");
          break;
        case "x":
          e.preventDefault();
          document.execCommand("cut");
        case "a":
          e.preventDefault();
          document.execCommand("selectAll");
      }
    }
  });
    
  // Listen fot postMessge events from grapes iframe
  window.addEventListener(
    "message",
    (event) => {
      if (event.data.target !== "nocode-builder") return;

      let bindConnMgr = bindingConnectorsManager.value;
      if( bindConnMgr == null )
        return;

      bindConnMgr.updateElementsPositions(event.data.payload);
    },
    false,
  );

  if(!apibase || !Configuration.appid ){
    return;
  }

  // Load the list of apps
  axios.get( apibase + 'apps' )
  .then( (result) => {

    // Fidn the requested app
    let myApp = result.data.applications.find( (app)=>{ 
      return app.id.value.toString() == Configuration.appid 
    } );
    // Store the general data about the app (as returned by /apps endpoint)
    let fullAppData = myApp;

    if( myApp != null ){
      // Load the app data from API
      axios.get( apibase + 'apps/' + Configuration.appid + '/pages'  )
      .then( (result) => {
        translateBindingTypes(result.data.pages);
        fullAppData.pages = result.data.pages;
        updateData(fullAppData);
        setTimeout( ()=>{
          // Set the current page based on the supplied view
          currentPage = result.data.pages[Configuration.currentPageIndex];
          let unsavedChanges = getStoredUnsavedChanges();
          let contentToLoad;
          if( unsavedChanges ){
            clearStoredUnsavedChanges();
            contentToLoad = unsavedChanges.beautifiedHtml;
            lastSavedHTMLCode = contentToLoad;
            canvasPanel.value.updateTemplateFile( contentToLoad );
          }else{
            let readTemplateFilePath = apibase + 'apps/' + Configuration.appid + '/edit?path=' + encodeURIComponent( currentPage.view );
            axios.get( readTemplateFilePath ).then( (result) => {
              // ====== Special case when editing inner element ======
              if( Configuration.elementid ){
                fullDocumentHTML = result.data.content;
                const parser = new DOMParser();
                const doc = parser.parseFromString(result.data.content, 'text/html');
                contentToLoad = doc.querySelector( "#"+Configuration.elementid).innerHTML;

                // Load css to merge when saving component
                let saveCssUrl = `${apibase}apps/${Configuration.appid}/edit?path=public/css/autogenerated.css`;
                axios.get( saveCssUrl ).then( (result) => {
                  fullDocumentCSS = result.data.content;
                }).catch( (error) => {
                  console.error('error loading full css file: ', saveCssUrl, error);
                });
              }else{
                contentToLoad = result.data.content;
              }
              // ====== End of Special case when editing inner element ======

              lastSavedHTMLCode = contentToLoad;
              canvasPanel.value.updateTemplateFile( contentToLoad );
            }).catch( (error) => {
              console.error('error loading template file: ', readTemplateFilePath, error);
            });
          }
        }, 300 );
      }).catch( (error) => {
        //console.log('error', error);
      });
    }
    
  }).catch( (error) => {
    //console.log('error', error);
  });

  statusMonitor.connect();
}



// Vue hooks
// ===============================================================================
onMounted( ()=>{
  init();
} );

</script>

