<template>
    <div class="logsPanel" style="position:relative;">
        <div class="toolBar">
            <div>
                <input type="checkbox" id="showServerLogs" name="showServerLogs" v-model="showServerLogs" /> GB Server
            </div>
            <div>
                <input type="checkbox" id="showClientLogs" name="showClientLogs" v-model="showClientLogs" /> Client
            </div>
            <div>
                <!-- select log level -->
                <select v-model="logLevel">
                    <!-- <option value="0">Debug</option> -->
                    <option value="1">Info</option>
                    <option value="2">Warning</option>
                    <option value="3">Error</option>
                </select>
            </div>
            <!--
            <div>
                <select v-model="loggedAppId">
                    <option v-for="item in loggedAppIdsMap" :value="item.id">{{ item.label }}</option>
                </select>
            </div> -->
            <div>
                Search: <input type="text" id="searchString" name="searchString" v-model="searchString" style="width: 200px;"/>
            </div>
            <div style="text-align: right; flex-grow: 1; flex-basis: 0;">
                <!-- <vscode-button @click="clearLogs">Clear</vscode-button> -->
                <button class="clearLogsButton" @click="clearLogs" title="Clear Logs">
                    <span class="codicon codicon-settings"><i aria-hidden="true" role="img" class="q-icon notranslate material-icons" style="font-size: 16px; padding: 5px;">delete</i></span>
                    <!-- <i aria-hidden="true" role="img" class="q-icon notranslate material-icons" style="font-size: 16px; padding: 5px;">Clear</i> -->
                </button>
            </div>
        </div>
        <div ref="logsContainer" class="logsContainer">
            <div :class="{logItem:true, levelInfo:item.level=='info', levelWarning:item.level=='warning', levelError:item.level=='error' }" v-for="(item, $index) in filteredLogs" :key="'logItem_'+$index">
                <div class="timeStamp">{{ item.timestamp }}</div>
                <!-- <div class="appSource">{{ item.appSource }}</div> -->
                <div class="logLevel">{{ item.level }}</div>
                <div :class="{logMessage:true, logMessageExpanded:item.expanded}" v-highlight="item.message" @click="item.expanded=!item.expanded"></div>
            </div>
        </div>
        
    </div>
</template>

<script setup>

import _ from 'underscore';
import {computed, ref} from 'vue';
import Configuration from '../../utils/Configuration.js'

const props = defineProps(['appData'])
const showServerLogs = ref(true);
const showClientLogs = ref(true);
const logLevel = ref(1);
const loggedAppId = ref('all');
const searchString = ref('');
const logsContainer = ref(null);

import hljs from 'highlight.js/lib/core';
import javascript from 'highlight.js/lib/languages/javascript';
import sql from 'highlight.js/lib/languages/sql';
import 'highlight.js/styles/atom-one-dark.css';

hljs.registerLanguage('javascript', javascript);
hljs.registerLanguage('sql', sql);

const highlight = {
  mounted(el, binding) {
    const code = binding.value || '';
    const highlightedCode = hljs.highlight('sql', code).value;
    el.innerHTML = `<pre style="white-space: pre-wrap; word-wrap: break-word;"><code style="display: block; width: 100%;">${highlightedCode}</code></pre>`;
  },
  updated(el, binding) {
    const code = binding.value || '';
    const highlightedCode = hljs.highlight('sql', code).value;
    el.innerHTML = `<pre style="white-space: pre-wrap; word-wrap: break-word;"><code style="display: block; width: 100%;">${highlightedCode}</code></pre>`;
  }
};

// Registering directive locally
const vHighlight = highlight;

const loggedAppIdsMap = ref([
    { id:'all', label:"All" },
    { id:0, label:"GB" },
])
const logLevelsMap = {
    //debug: 0,
    info: 1,
    warning: 2,
    error: 3
}
// debug, info, wanring, error
const logs = ref([]);
window.logs = logs;

const filteredLogs = computed( ()=>{
    let results = logs.value.filter( (item)=>{
        //debugger;
        let matchesSource = (item.source=="server" && showServerLogs.value) || (item.source=="client" && showClientLogs.value );
        let matchesLevel = logLevelsMap[item.level] >= logLevel.value;
        let matchesAppId = loggedAppId.value == 'all' || item.appid == loggedAppId.value;
        let matchesSearch = searchString.value == '' || item.message.toLowerCase().indexOf(searchString.value.toLowerCase() ) >= 0;
        return matchesSource && matchesLevel && matchesSearch && matchesAppId;
    })
    return results;
})

function clearLogs(){
    logs.value = [];
}

function addLog( logObject ){
    // Discard messages with appId and not matching the current app id
    let configuredAppId = Configuration.appid;
    let messageAppId = logObject.appid;
    if( messageAppId != undefined && messageAppId != configuredAppId ){
        return;
    }
    let logPrefix = 'log:message ';
    if( logObject.message.indexOf( logPrefix ) == 0 ){
        // Add the app id to the loggedAppIdsMap if it doesn't exist
        if( !loggedAppIdsMap.value.find( (item)=>{ return item.id == logObject.appid } ) ){
            loggedAppIdsMap.value.push( { id:logObject.appid, label:logObject.appid } );
        }
        
        // Remove the logPrefix from the message
        let message = logObject.message.slice( logPrefix.length );

        // Extract the log level from the message text
        if( message.indexOf("Info: ") == 0 ){
            logObject.level = "info";
            message = message.slice( "Info: ".length );
        }else if( message.indexOf("Warning: ") == 0 ){
            logObject.level = "warning";
            message = message.slice( "Warning: ".length );
        }else if( message.indexOf("Error: ") == 0 ){
            logObject.level = "error";
            message = message.slice( "Error: ".length );
        }


        // Replace newlines with <br/>
        //message = message.split("\n").join("<br/>");
        message = message.split(" ").join("");
        logObject.message = message;
        
        // Parse app source-id and timestamp
        logObject.appSource = logObject.appid == "0" ? "GB" : "app "+logObject.appid;
        logObject.timestamp = logObject.timestamp.replace("T", " ").replace("Z", "").split(".")[0];

        // set collapsd view by default
        logObject.expanded = false;

        // Add the log to the logs array
        logs.value.push( logObject );

        // Scroll to bottom so that latest log is visible
        logsContainer.value.scrollTop = logsContainer.value.scrollHeight;
    }
}

defineExpose( {addLog} );
</script>

<style lang="scss" scoped>
@import "../../styles/globalStyles.scss";


.toolBar{
    display: flex;
    flex-direction: row;
    font-size: 12px;
    padding-bottom: 5px;
    border-bottom: solid 1px #ccc;
    margin-bottom: 10px;
    gap: 15px;
}


.logsContainer{
    height: 200px;
    overflow: auto;
}

.logItem:hover{
    background-color: #00000010;
}
.logItem{
    font-size: 14px;
    color: #828282;
    display: flex;
    flex-direction: row;
    gap: 20px;
    align-items: flex-start;
    border-bottom: solid 1px #cccccc20;
}

.timeStamp, .appSource, .logLevel, .logMessage {
    flex-shrink: 0; /* Ensure they don't shrink below their designated width */
    overflow: hidden; /* For text-overflow, if needed */
    
    text-overflow: ellipsis; /* Ellipsis if content overflows */
}

.timeStamp{
    width: 150px;
}
.logLevel{
    width: 60px;
}
.appSource{
    width: 50px;
}


.logMessage {
    flex-grow: 1;
    flex-basis: 0;
    min-width: 0;
    overflow-wrap: break-word;
    max-width: calc(100% - 260px - 60px); /* Subtract the total width of the other columns and gap */
    overflow: hidden; 
    max-height: 20px;
    cursor: pointer;
}
.logMessageExpanded{
    overflow: unset; 
    max-height: unset;
}
.levelInfo{
    color: #49afed;
}
.levelWarning{
    color: #e29a42;
}
.levelError{
    color: rgb(242, 91, 91);
}

.clearLogsButton{
    opacity: 0.5;
}
.clearLogsButton:hover{
    opacity: 1;
}
</style>