<template>
    <div style="position: relative;">
        <div class="title" @click="toggleExpand">{{title}}</div>
        <div class="expandBtn" @click="toggleExpand">
            <span v-if="!expanded"><i class="fa fa-chevron-right"></i></span>
            <span v-if="expanded"><i class="fa fa-chevron-down"></i></span>
        </div>
    </div>
</template>



<script setup>

import { ref, watch} from 'vue';
const props = defineProps(['title', 'expanded'])
let emit = defineEmits(['expand'])
let expanded = ref(props.expanded)

function toggleExpand(){
    emit('expand', expanded.value)
}


watch( props, (props)=>{
    expanded.value = props.expanded;
})

</script>



<style lang="scss" scoped>
@import "../../styles/globalStyles.scss";

.title{
    line-height: 22px;
    color: var(--sideBarSectionHeader-foreground);
    background-color: var(--sideBarSectionHeader-background);
    border-top: 1px solid var(--sideBarSectionHeader-border);
    height: 22px;
    font-size: 11px;
    font-weight: 700;
    overflow: hidden;
    display: flex;
    cursor: pointer;
    align-items: center;
    box-sizing: border-box;
    text-transform: uppercase;
    padding-left: 10px;
}

.expandBtn{
    position: absolute;
    right: 10px;
    top: 2px;
    opacity: 0.6;
    font-size: 13px;
    cursor: pointer;
}
</style>