function getFieldsOfDataFrameType(){
  let dsFields = props.appData.pages[Configuration.currentPageIndex].model.fields.filter( (field)=>{ 
    return  field.type == "DataFrames.DataFrame" || 
            field.type == "PlotlyData" || 
            field.type == "DataTable.DataFrames.DataFrame"
  } );
  //debugger;
  dataSourceFields.value = dsFields;
}

// ====================== COMPUTED ====================== 

const getAllModelFields = computed(() => {
  let fields = props.appData.pages[Configuration.currentPageIndex].model.fields;
  let result = [];
  fields.forEach( (field)=>{
    if( field.type == 'DataTable.DataFrames.DataFrame' ){
      let props = field.props;
      if( props ){
        props.forEach( (prop)=>{
          result.push( { name:field.name + '.' + prop, type:field.type } );
        } )
      }
    }else if( field.type == 'DataFrames.DataFrame' ){
        let columns = field.columns;
        if( columns ){
          columns.forEach( (column)=>{
            result.push( { name:field.name + '.' + column, type:field.type } );
          } )
        }
      }
    result.push( { name:field.name, type:field.type } );
  } )
  // sort alphabetically ignoring case
  result.sort( (a,b)=>{
    return a.name.toLowerCase().localeCompare(b.name.toLowerCase());
  } )
  return result;
  //return ['uno', 'dos', 'tres'];
})

const getSelectedDataSourceColumns = computed(() => {
  let selectedDataSourceColumns = [];

  // Extract columns or props from dataframes
  console.log( 'dataSourceFields', dataSourceFields );
  dataSourceFields.value.forEach( (field)=>{
    // Dataframes expected to have columns
    if( field.type.indexOf( 'DataFrames.DataFrame') == 0 ){
      let columns = field.columns;
      if( columns ){

        columns.forEach( (column)=>{
          selectedDataSourceColumns.push( field.name + "." + column );
        } )
      }
    }
    // DataTables expected to have props
    let props = field.props;
    if( props )
    props.forEach( (prop)=>{
      selectedDataSourceColumns.push( field.name + "." + prop );
    } )
  } )
  // add array-type fields
  props.appData.pages[Configuration.currentPageIndex].model.fields.forEach( (field)=>{
    if( field.type == "Array" ){
      selectedDataSourceColumns.push( field.name );
    }
  } )



  /* let selectedDataSourceName = selectedDataSource.value;
  if( selectedDataSourceName ){
    let selectedDataSourceField = dataSourceFields.value.find( (field)=>{ return field.name == selectedDataSourceName } );
    if( selectedDataSourceField ){
      selectedDataSourceColumns = selectedDataSourceField.columns;
    }
  } */
  return selectedDataSourceColumns;
})

function availableDataProperties(defObject, destination) {
  let notUsedProperties = defObject.filter( (propDef)=>{
    let isUsed = false;
    let existingProperty = getNestedProperty(destination, propDef.id);
    if( existingProperty != null ){
      isUsed = true;
    }    
    return !isUsed;
  })
  return notUsedProperties;
};

const availableConfigProperties = computed(() => {
  let notUsedProperties = configPropertiesDefinition.value.filter( (propDef)=>{
    let isUsed = false;
    let existingProperty = getNestedProperty(addPropertyDestination.value, propDef.id);
    if( existingProperty != null ){
      isUsed = true;
    }    
    return !isUsed;
  })
  return notUsedProperties;
})





