<script setup>
    import {chartTypes} from '../utils/chartDefinitions.js';
  import { ref, computed } from 'vue';

  // PROPS and EMITS
  const props = defineProps(['dataSourceColumns', 'treeObject', 'propertiesDefinition', 'appData', 'modelFields', 'sectionIndex', 'showSectionTitle'])
  const emit = defineEmits(['change'])

  let currentGbType = ref[null];

console.log( "ChartSection.modelFields: ", props.modelFields)

    function handleArrayInput(property, inputValue) {
        try {
            const parsedValue = JSON.parse(inputValue);
            if (Array.isArray(parsedValue)) {
                property.valueHolderParent[property.propName] = parsedValue;
            } else {
                // To-do: show UI to inform user about invalid value
                console.error("Input is not an array.");
            }
        } catch (e) {
            // To-do: show UI to inform user about invalid value
            console.error("Failed to parse input as JSON:", e);
        }
    }

  /*  ======================================================================================
                                    COMPONENT METHODS
    ====================================================================================== */
    /* function getPropVal(property){
        console.log( 'getPropVal', property.name, property );
        let value = property.valueHolderParent[property.propName]
        console.log( 'getPropVal value: ', value );
        return value;
    } */
const getChartTypeIcon = computed( ()=>{
    let chartTypeDef = chartTypes.find( (chartType)=>{ return chartType.id == currentGbType } );
    console.log( 'chartTypeDeffffff', chartTypeDef );
    if( !chartTypeDef ){
        console.error( "Could not find chart type definition for: " + currentGbType );
        return "scatterChart";
    }else{
        return chartTypeDef.icon;
    
    }
})

    function removeSection(){
        let sectionIndex = props.sectionIndex;
        emit('removeTrace', sectionIndex);
    }
    function removeTraceProperty( property ){
        let propertyId = property.id;
        console.log( 'removeTraceProperty before: ', property, props.treeObject );
        removePropertyPath( props.treeObject, propertyId );        
        console.log( 'removeTraceProperty after: ', property, props.treeObject );
        submitChanges();
        //let sectionIndex = props.sectionIndex;
        //emit('removeTrace', sectionIndex);
    }

    function removePropertyPath(obj, propertyPath) {
        // Split the property path into parts
        const parts = propertyPath.split('.');

        // Recursive function to delete properties
        function deleteProp(currentObj, pathIndex) {
            const prop = parts[pathIndex];

            // Base case: if we've reached the last part of the path
            if (pathIndex === parts.length - 1) {
                delete currentObj[prop];
                return Object.keys(currentObj).length === 0;
            }

            // Recursive case: go deeper if the property exists
            if (currentObj[prop] !== undefined) {
                const shouldDeleteParent = deleteProp(currentObj[prop], pathIndex + 1);

                // Delete the parent property if it's now empty
                if (shouldDeleteParent) {
                    delete currentObj[prop];
                    return Object.keys(currentObj).length === 0;
                }
            }

            return false;
        }

        // Start the recursive deletion process
        deleteProp(obj, 0);
    }

    /**
     * Toggles the attribute mode from constant to binding and viceversa
     * @param {string} key - The name of the attribute.
     * @param {string} value - The value of the attribute.
     */
    function toggleDynamicAttribute(property){
        console.log( 'toggleDynamicAttribute', property );
        let currentValue = property.valueHolderParent[property.propName];
        let newKey;
        if( currentValue.indexOf('$_{')==0 ){
            // Switch to constant
            newKey = currentValue.replace( '$_{', '' );
            // remove '}' char at the end
            newKey = newKey.substring(0, newKey.length - 1);
        }else{
            // Switch to binding
            newKey = '$_{' + currentValue + '}';
        }

        property.valueHolderParent[property.propName] = newKey;
        submitChanges();
        /* 
        let newKey = key.indexOf('gb_b__') == 0 ? key.replace( 'gb_b__', 'gb_c__' ) : key.replace( 'gb_c__', 'gb_b__' );
        component.value.removeAttributes( [key]);
        let newAtts = {};
        // When switching to Binding mode, pick the model variable with the same name set as constant or,
        // If it doesn't exist, pick one of the same type by default
        if( newKey.indexOf('gb_b__') == 0 ){
            if(modelFieldsByName[value] != null )
                newAtts[newKey] = value;
            else{
                let cleanAttributeName = getAttributeCleanRealName(newKey).toLowerCase();
                let def = getBlockPropertyDefinition(cleanAttributeName);

                let attributeObject = { def }
                let sameTypeModelFields = filteredModelFields(attributeObject);
                if( sameTypeModelFields.length > 0 )
                    newAtts[newKey] = sameTypeModelFields[0].name;
                else
                    newAtts[newKey] = "Not Set";
            }
        }else{
            newAtts[newKey] = value;
        }

        component.value.addAttributes(newAtts);
        submitChanges(); */
    }

    function isPropValueBinding( property ){
        console.log( 'isPropValueBinding', property.propName, property );
        let value = property.valueHolderParent[property.propName];
        return typeof value == 'string' && value.indexOf('$_{')==0;
    }
    
    //function changeChartType( property, newType){
    function changeChartType( event){
        console.log( 'changeChartType', currentGbType, props.treeObject );
        console.log( 'chartTypes', chartTypes );
        let chartTypeDef = chartTypes.find( (chartType)=>{ return chartType.id == currentGbType } );
        console.log( 'chartTypeDef', chartTypeDef );
        // store current props in temp object
        let tempProps = {};
        for( let prop in props.treeObject ){
            tempProps[prop] = props.treeObject[prop];
        }
        // remove all properties from props.treeObject
        for( let prop in props.treeObject ){
            delete props.treeObject[prop];
        }
        // assign props from template. use tempobject if prop matches
        for( let prop in chartTypeDef.traceTemplate ){
            if( tempProps[prop] != undefined && prop != "type" && prop != "gbtype"  )
                props.treeObject[prop] = tempProps[prop];
            else
                props.treeObject[prop] = chartTypeDef.traceTemplate[prop];
        }
        console.log( 'props.treeObject FINAL:', props.treeObject );
        // assign props from temp object
        props.treeObject.gbtype = currentGbType;
        submitChanges();
        return;
        let oldType = property.valueHolderParent[property.propName];
        console.log( 'changeChartType', newType, props );
        if( newType == property.valueHolderParent[property.propName] ){
            console.log( "same chart type chosen, skipping..." );
            return;
        }
        // Transform trace based on type
        if( newType == 'pie' ){
         console.log( "transforming to pie chart" );
         props.treeObject.labels = props.treeObject.x;
         props.treeObject.values = props.treeObject.y;
            props.treeObject.x = undefined;
            props.treeObject.y = undefined;
            delete props.treeObject.x;
            delete props.treeObject.y;
        }else if( oldType == 'pie' && (newType != 'pie' ) ){
            console.log( "transforming FROM pie chart" );
            props.treeObject.x = props.treeObject.labels;
            props.treeObject.y = props.treeObject.values;
            props.treeObject.labels = undefined;
            props.treeObject.values = undefined;
            delete props.treeObject.labels;
            delete props.treeObject.values;
        }
        
        property.valueHolderParent[property.propName] = newType;
        submitChanges();
    }
    
  function submitChanges(){
    emit('change', props.treeObject);
  }

  function getNestedPropertyParent(obj, propName) {
    const propParts = propName.split('.');
    if( propParts.length == 1 )
      return obj;

    for (let i = 0; i < propParts.length-1; i++) {
        if (!obj || !obj.hasOwnProperty(propParts[i])) {
            return undefined;
        }
        obj = obj[propParts[i]];
    }
    return obj;
}

function getNestedProperty(obj, propName) {
    const propParts = propName.split('.');
    for (let i = 0; i < propParts.length; i++) {
        if (!obj || !obj.hasOwnProperty(propParts[i])) {
            return undefined;
        }
        obj = obj[propParts[i]];
    }
    return obj;
}

const definitionsListWithValues = computed( ()=>{
    let result = [];
    for( let i = 0; i < props.propertiesDefinition.length; i++ ){
        let propertyDefCloned = { ...props.propertiesDefinition[i] };
        let value = getNestedProperty( props.treeObject, propertyDefCloned.id );
        if( value !== undefined ){
            propertyDefCloned.valueHolderParent = getNestedPropertyParent( props.treeObject, propertyDefCloned.id );
            propertyDefCloned.propName = propertyDefCloned.id.split('.').pop();
            result.push( propertyDefCloned );
        }
    }
    console.log( 'definitionsListWithValues', result );
    return result;
});
  
</script>
  

