<template>
    <div class="blocker" ref="dialogBlocker" @click="blockerClicked($event)">        
        <div class="addPropsPanel">
            <div style="text-align: center; font-size: 0.8em; font-weight: bold; margin-bottom: 10px;">Add Property</div>
            <div class="closeAddProp" @click="closeButtonClicked">X</div>
            <div>
                <vscode-text-field 
                    id="addPropertyDialogSearch"
                    :value="searchTerm"
                    @input="(event)=>searchTermChanged(event)" 
                    @keyup.escape="resetSearch"
                    placeholder="Search Properties" style="padding: 5px 10px; width: 100%;"></vscode-text-field>
                    <i class="fas fa-search" style="position:absolute; top: 47px; right: 15px; opacity: 0.5;"></i>
                <!-- <vscode-text-field 
                    :value="searchTerm"
                    @input="(event)=>buildCategoriesTree(event)" placeholder="Search UI Components" style="padding: 5px 10px; width: 90%;"></vscode-text-field> -->
            </div>
            <div class="addPropsList">
                <div class="addPropRow" v-for="item in sortedPropsList()" @click="addProperty(item)" :title="item.desc||'Property description not available'">
                    <div style="width:70%;">{{item.label}}</div>
                    <div style="    text-transform: capitalize;    width: 30%; font-size: 0.8em; overflow: hidden; white-space: nowrap; text-overflow: ellipsis;     display: flex; justify-content: right; align-items: center; opacity: 0.6;">{{item.type}}</div>
                </div>
            </div>
        </div>
    </div>
</template>


<script setup>
import _ from 'underscore';
import { ref, onMounted } from 'vue'

// Define props and emits
const props = defineProps(['propslist', 'defObject', 'destination']);
const emit = defineEmits(['propAdded', 'close' ]);

// Define reactive variables
const searchTerm = ref('');
const dialogBlocker = ref('');


// Event handlers
// ---------------------------------------------------
function blockerClicked(event){
    if( event.target == dialogBlocker.value){
        closeButtonClicked();
    }
}

function closeButtonClicked(){
    emit('close');    
}

// Component methods
// ---------------------------------------------------
function resetSearch(){
    searchTerm.value = '';
}

function searchTermChanged(event){
    searchTerm.value = event.target.value;
}


function addProperty(item){
    emit('propAdded', item, props.defObject, props.destination);
}

function sortedPropsList(){
    let results = props.propslist;
    // Apply filtering if search term is present
    if( searchTerm.value != '' ){
        results = results.filter((item)=>{
            return item.label.toLowerCase().indexOf(searchTerm.value.toLowerCase()) > -1;
        });
    }
    // Sort the results by label
    return _.sortBy(results, (item)=>{ return item.label.toLowerCase() });
}



onMounted(()=>{
    document.querySelector('#addPropertyDialogSearch').focus();
});

</script>

<style scoped>

.blocker{
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    background-color: rgb(0 0 0 / 0.5);
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.addPropsPanel{
    background-color: var(--background);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1882352941);
    border-radius: 5px;
    padding: 10px 0px;
    width: 300px;
    min-height: 200px;
    max-height: 500px;
    display: flex;
    flex-direction: column;
    position: relative;    
    border: solid 1px #ffffff20;
}

.closeAddProp{
    position: absolute;
    right: 10px;
    top: 5px;
    cursor: pointer;
    font-weight: bold;
    font-size: 12px;
    color: #999;
}

.addPropsList{
    padding: 10px 10px;
    position: relative;
    height: 100%;
    overflow: auto;
}

.addPropRow{
    padding: 2px;
    border-bottom: solid 1px rgb(0 0 0 / 17%);
    opacity: 0.9;
    cursor: pointer;
    position: relative;
    font-size: 0.9em;
    display: flex;
    flex-direction: row;
    gap: 5px;
}

.addPropRow:hover{
    opacity: 1;
    background-color:#f4f4f410; 
}

.addPropRow:hover >.addPropIcon{
    opacity: 1;
}
</style>