<template>
    <div>
        <div v-if="!isPro" class="notProUI">
        The AI Assistant is a Pro feature.<br/>
        Upgrade to Pro and start using it now!<br/> <br/>
        <vscode-button @click="showUpgradeLicenseDialog" style="margin-right: 3px;">Upgrade</vscode-button> 
        </div>

        <div v-if="isPro">
            <div v-if="!component" class="nothingSelected">
                <i>Nothing selected</i>
            </div>

            <div v-else style="position:relative;">
                <div class="flex flex-col items-center justify-center p-6" style="padding: 0px;">
                    <div class="mb-4 w-full max-w-xl">
                        <textarea rows="2" v-model="userPrompt" :disabled="isLoading" placeholder="Select a UI component and ask AI to style or edit it"
                            class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" />
                    </div>

                    <div v-if="requestObject.aiError" class="">
                        <p class="text-red-600" style="margin-top: -15px;">{{ requestObject.aiError }}</p>
                    </div>

                    <vscode-button v-if="!isLoading" @click="aiSendClicked" style="width: 100px !important;">Ask AI</vscode-button>
                    <vscode-button v-if="isLoading"  disabled style="width: 100px !important;">Plese wait...</vscode-button>

                    <!-- to-do: this text and link is also used in the AI docs panel. Ideally we should store the text and link in a shared variable. But vue won't listen to the click events within the v-html, and regular links will be blocked by vscode. So by now we are adding the text and link here and in aidocs -->
                    <div class="terms" style="margin-top: 10px;">
                    Use of the AI features implies acceptance of the Genie AI <span @click="openLink( Configuration.TERMS_OF_USE_URL )" style="text-decoration: underline;">terms of use</span>. AI results can be incorrect.
                    </div>
                    <!-- insert svg file -->
                    <!-- <img v-if="isLoading" src="img/icons/loading_58.svg" alt="Loading" style="position: relative; bottom: 0; right: 0; width: 58px; height: 58px;"/> -->
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>

import _ from 'underscore';
import axios from 'axios';
import {computed, ref} from 'vue';
import Configuration from '../../utils/Configuration.js'
import HtmlTemplateParser from '../../utils/HtmlTemplateParser';

const props = defineProps(['appData', 'selectedComponent'])

const isPro = computed(() => {
    return Configuration.IS_PRO;
});

let component = ref(null);
let userPrompt = ref("");
let isLoading = ref(false);
let aiApiUrl = Configuration.ask_ai_url + 'codegen';
let requestObject = ref({});

/**
 * Sets the selected component.
 * @param {Object} newComponent 
 */
function setSelectedComponent(newComponent){
    component.value = newComponent;
}

function showUpgradeLicenseDialog(){
    window.showUpgradeLicenseDialog(false);
}

function applyCssStylesToHtml(html, cssString) {
    // Parse the CSS string to extract styles for ID selectors
    const styleRules = cssString.match(/#[^\{]+\{[^\}]+\}/g);
    const stylesById = {};

    if (styleRules) {
        styleRules.forEach(rule => {
            const parts = rule.split('{');
            const id = parts[0].trim().substring(1); // Remove '#' at the start
            const styles = parts[1].trim().slice(0, -1); // Remove closing '}'
            stylesById[id] = styles;
        });
    }

    // Create a DOM parser to parse the HTML string
    const parser = new DOMParser();
    const doc = parser.parseFromString(`<body>${html}</body>`, 'text/html');

    // Apply the styles to the elements with the corresponding IDs
    Object.keys(stylesById).forEach(id => {
        const element = doc.getElementById(id);
        if (element) {
            element.style.cssText += stylesById[id];
        }
    });

    // Extract and return the inner HTML of the body, excluding the automatically added tags
    return doc.body.innerHTML;
}

function openLink(url){
    window.parent.postMessage( {command:"openLink", url:url }, "*" );
}

function sendRequestToAiPreservingPlotlyAttributes(inputHtml, attributes, onComplete) {
    
  // Create a DOMParser to parse HTML strings
  const parser = new DOMParser();
  const serializer = new XMLSerializer();

  // Parse the initial HTML code to a DOM
  const initialDOM = parser.parseFromString(inputHtml, 'text/html');

  // Store the attributes of <plotly> nodes
  const plotlyAttributes = {};
  initialDOM.querySelectorAll('plotly').forEach(node => {
    attributes.forEach(attr => {
      if (node.hasAttribute(attr)) {
        const nodeId = node.id; // Assuming each <plotly> node has a unique ID
        if (!plotlyAttributes[nodeId]) {
          plotlyAttributes[nodeId] = {};
        }
        plotlyAttributes[nodeId][attr] = node.getAttribute(attr);
      }
    });
  });

    // Add css styles as inline styles to the html
    let htmlWithStyles = applyCssStylesToHtml( inputHtml, editor.getCss() );

    // Inform the request is started (loading)
    emit("aiCodeChanged", 'loading');    
    isLoading.value = true;

    // Send the AJAX request
    axios.post( aiApiUrl, 
        { 
            content_type: "html", 
            sessionid: Configuration.SESSIONID, 
            prompt: requestObject.value.userPrompt, 
            code: htmlWithStyles
        }
    )
    .then( (response)=>{
        // Inform the request is finished (not loading)
        isLoading.value = false;

        // Parse the response
        let responseObject = JSON.parse( response.request.response )

        // Handle errors
        if( responseObject.error ){
            requestObject.value.aiError = responseObject.error;
            emit("aiCodeChanged", 'hidden');
        
        }else{
        // handle success

            // reset prompt and loading state
            isLoading.value = false;
            userPrompt.value = "";

            // Restore the attributes for each <plotly> node using stored values
            let receivedHtml = responseObject.response;
            let parser = new DOMParser();
            const newDOM = parser.parseFromString(receivedHtml, 'text/html');

            Object.keys(plotlyAttributes).forEach(nodeId => {
                const node = newDOM.getElementById(nodeId);
                if (node) { // Check if the node still exists
                    Object.entries(plotlyAttributes[nodeId]).forEach(([attr, value]) => {
                        node.setAttribute(attr, value);
                    });
                }
            });

            // Serialize the DOM back to an HTML string
            let restoredHtml = serializer.serializeToString(newDOM.body);
/* 
            // Make sure all blocks have mandatory attributes if missing
            let mandatoryDefs = HtmlTemplateParser.generateBlocksMandatoryProperties();
            let filledHtml = HtmlTemplateParser.addMandatoryProperties( restoredHtml, mandatoryDefs );
             */
            let cleanHtml;
            try{
                cleanHtml = HtmlTemplateParser.cleanHtml( restoredHtml );
            }catch(e){
                requestObject.value.aiError = e.message;
                emit("aiCodeChanged", 'hidden');
            }
            // encode the html
            // ------------------------------------------
            const doc = parser.parseFromString(cleanHtml, 'text/html');
            const firstDiv = doc.body || null;      
            HtmlTemplateParser.encodeAttributesInDOM(firstDiv );    
            const firstDivContent = firstDiv.innerHTML;
            requestObject.value.aiApiResponse = firstDivContent;
            
            // Update the selected element in the canvas and inform
            
            window.lastHtmlBeforeAiReplace = editor.getHtml();
            //editor.UndoManager.stop();
            if( editor.getSelected().attributes.attributes.id == "gbTemplateContainer" ){
                editor.getSelected().components(firstDivContent);
            }else{
                editor.getSelected().replaceWith( firstDivContent );
            }
                
            emit("aiCodeChanged", 'ready');
            
        }
    } )
    .catch( (err)=>{
        // Inform the request is finished with an error
        isLoading.value = false;
        requestObject.value.aiError = err.message;
        emit("aiCodeChanged", 'hidden');
    } )
}

function aiSendClicked(){
    let selectedElement = component.value;

    let selectedHtml = selectedElement.toHTML();

    let componentCss = editor.getCss({   
                                    component: selectedElement, //editor.getSelected(), 
                                    keepUnusedStyles:false, 
                                    avoidProtected: true 
                                });


    let bindingsList = appData.value.pages[Configuration.currentPageIndex].model.fields.map (el=>{
        let result = { name:el.name, type:el.type };
        if( el.columns != null ){
            result.columns = el.columns.map(column=>column);
        }
        return result;
    } );
    bindingsList = JSON.stringify( bindingsList, null, 4 );

    let extendedPrompt = `
User Request: ${userPrompt.value}

Instructions for AI:
1. Layout: Utilize DIV elements with "row" and "st-col col-x col-sm st-module" classes for creating a responsive layout. The layout should be divided into the sections described above. For columns, ("col-x"), "x" is a number between 1 and 12. 
2. Components: Wherever possible, employ Quasar 1 components for UI elements. For date selection, use 'q-date' instead of standard HTML input components. Refer to the supported components list provided below.
3. never nest a "row" directly as a child of a "row" or a "col" directly as a child of a "col".
4. For 'q-table' elements, bind data using only "DataTable" or Array types, never DataFrames.
5. Data Binding: Properly assign data to UI components, ensuring mandatory attributes like 'v-model' are used for input components such as textfields, select, and checkbox. 
6. Rules:
   - Avoid using '<template>' tags.
   - In dynamic attributes (starting with ':'), do not use interpolated values (e.g., avoid '{{value}}').
   - Never return an element with a CSS class whose name is or starts with "gbTemplateContainer"
7. Other components: 
7.1. For "big numbers" use this component template (use colon in attributes for bindings if necessary):
<st-big-number number="0" title="Label"></st-big-number>
7.2. For date pickers use this component template (use colon in attributes for bindings if necessary, and note that v-model value must match for the INPUT and the q-date components):
<q-input iscomposite="true" v-model="undefined" clearable="true" filled="true" label="Pick a date"><q-icon class="cursor-pointer" name="event" style="height: 100%;"><q-popup-proxy><q-date v-model="undefined" mask="YYYY-MM-DD" no-unset="true" today-btn="true"></q-date></q-popup-proxy></q-icon></q-input>
7.3. For date pickers use this component template (use colon in attributes for bindings if necessary, and note that v-model value must match for the INPUT and the q-date components):
<q-input v-model="undefined" clearable="true" filled="true" label="Pick a time"><q-icon class="cursor-pointer" name="alarm" style="height: 100%;"><q-popup-proxy><q-time v-model="undefined" mask="HH:mm:ss"></q-time></q-popup-proxy></q-icon></q-input>
7.4. For charts, use Plotly library, with this component template below, replacing binding names as needed and preserving the $_{} syntax found in the template. When binding to DataFrame data, never bind directly to it, always bind to a nested column (i.e. myDataFrame.myColumn), and use the syntax found in the template ( i.e. $_{myDataFrame.myColumn} ). Do not use the colon ":" prefix inside attribute values, that's only for dynamic attribute names when appropriate. Make sure the plot data is properly bsae64 encoded.
8. Styles should always be applied inline. If user provides current css styles (see below), make sure to apply them inline to your answer, unless user requests to change or remove them,

<plotly data="W3siZ2J0eXBlIjoiU2NhdHRlciIsIngiOiIkX3tCSU5ESU5HX05BTUV9IiwieSI6IiRfe0FOT1RIRVJfQklORElOR19OQU1FfSIsInR5cGUiOiJzY2F0dGVyIiwibW9kZSI6ImxpbmVzIiwibmFtZSI6IlRyYWNlIn1d" config="e30=" layout="eyJ0aXRsZSI6Ik15IENoYXJ0In0="></plotly>

Available Data (Bindings):
${bindingsList}

Supported Quasar Components:
["q-input", "q-btn", "q-select", "q-radio", "q-checkbox", "q-toggle", "q-slider", "q-range", "q-date", "q-time", "q-knob", "q-list", "q-table", "q-img", "q-video", "q-avatar", "q-badge", "q-chip", "q-icon", "q-rating", "q-spinner", "q-timeline", "q-timeline-entry", "q-scroll-area"]

Related CSS styles:
${componentCss}
`;

/* 
    IMPORTANT:
Use columns and rows (DIV elements with "row"/"col" classes), to generate beautiful layouts (i.e. using rows to separate SVGTextContentElement, columns to distribute controls and viuews, etc). 

Add the appropriate UI components based on the purpose of the dashboard and the available data. Whenever possible, use Quasar 1 components (i.e.q-date instead of html inpout component) but only from the supported list above.

Please also assign the appropriate data to the UI components, making sure that mandatory attributes are properly assigned (i.e v-model for input components such as textfields, select, checkbox, etc).

Some rules you need to apply:

- For "data" attribute value of "q-table" elements, use only bindings of type-family "DataTable" (i.e. DataTable.DataFrames) or Array, and never use bindings of type-family "DataFrames" (i.e. DataFrames.DataFrames).


- never include <template> nodes

- in dynamic attributes (starting with colon), never use interpolated values (i.e. {{value}})


Here's the available data (so-called "bindings" ) and their types:
[
    { "name": "avg_price", "type": "Number" },
    { "name": "end_date", "type": "String" },
    { "name": "end_price", "type": "Number" },
    { "name": "fileuploads", "type": "UnknownType" },
    { "name": "isprocessing", "type": "Boolean" },
    { "name": "isready", "type": "Boolean" },
    { "name": "percent_return", "type": "Number" },
    { "name": "period_diff", "type": "Number" },
    { "name": "prices", "type": "DataFrames.DataFrame", "columns": [ "stock","close", "timestamp", "ma20" ] },
    {
        "name": "prices_table",
        "type": "DataTable.DataFrames.DataFrame",
        "columns": []
    }, 
    { "name": "selected_stock", "type": "String" },
    { "name": "start_date", "type": "String" },
    { "name": "stocks", "type": "Array" }
]

Here's the list of supported Quasar components:
[ "q-input", "q-btn", "q-select", "q-radio", "q-checkbox", "q-toggle", "q-slider", "q-range", "q-date", "q-time", "q-knob", "q-list", "q-table", "q-img", "q-video", "q-avatar", "q-badge", "q-chip", "q-icon", "q-rating", "q-spinner", "q-timeline", "q-timeline-entry", "q-scroll-area" ]
`;
 */
    requestObject.value = {
        selectedHtml: selectedHtml, 
        userPrompt: extendedPrompt, 
    };
    requestObject.value.previousPrompt = userPrompt.value;


    // Decode the html
    // ------------------------------------------
    const entireHtml = requestObject.value.selectedHtml;
    const parser = new DOMParser();
    const doc = parser.parseFromString(entireHtml, 'text/html');
    const firstDiv = doc.body || null;      
    HtmlTemplateParser.decodeAttributesInDOM(firstDiv );    
    const firstDivContent = firstDiv.innerHTML;
    // ------------------------------------------
    // End of decoding html
    // ------------------------------------------

        
    sendRequestToAiPreservingPlotlyAttributes(firstDivContent, ["data", "config", "layout"], (resultHtml) => {
        //console.log( 'aiCodePanel :: sendRequestToAiPreservingPlotlyAttributes-resultHtml: ', resultHtml );
    });


  window.logUserEvent( "ai-code-panel::ask", { 
  });
    
}


defineExpose( { setSelectedComponent } );
const emit = defineEmits(["aiCodeChanged"]);
</script>

