<template>
    <div class="componentAddDialogContainer">        
        <div class="componentAddDialog">
            <div class="closeButton" @click="closeDialog(true)">X</div>
            <p style="    margin-bottom: 6px; font-size: 12px; font-weight: 400; opacity: 0.7;">Add component for binding</p>
            <div style="padding-bottom: 10px; border-bottom: 1px solid #cccccc50;">
                <img :src="'img/icons/type_'+props.componentPlaceHolderData.datatype+'.png'" style="height: 17px; vertical-align: middle; display: inline-block;" />
                {{ props.componentPlaceHolderData.bindingname }} 
            </div>
            <div style="font-size: 14px; padding: 20px 0px 15px">Suggested Components:</div>
            <div class="compGroupsContainer">
                <div :class="{ compGroup:true, compGroupDisabled: comp.proRequired && !isPro }" v-for="comp in props.componentPlaceHolderData.components" :key="comp.type"  @click="(!comp.proRequired) || ( comp.proRequired  && isPro ) ? selectCompProp(comp, comp.matchingProperties[0]) : showUpgradeLicenseDialog(true)" :title="comp.proRequired && !isPro ? 'Upgrade to PRO plan to use this component and many more!' : comp.label">
                    <div class="compName">
                        <div :class="{ compNameLabel: true, compNameLabelProRequired: (comp.proRequired && !isPro) }">{{ comp.label }}</div>
                        <div :class="{ compNameIconContainer: true, compNameIconContainerProRequired: (comp.proRequired && !isPro) }">
                            <img :src="getBlockIconPath(comp)" :class="{ typeIcon:true, typeIconProRequired: (comp.proRequired && !isPro) }" style="filter: var(--library-icon-filter); max-height: 20px; margin-top: 6px; pointer-events: none;" />
                           <!--  <div>Pro: {{ comp.proRequired }}</div> -->
                        </div>
                        <img v-if="comp.proRequired && !isPro" src="img/icons/pro_badge.png" class="proBadge" style=""/>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script setup>
import Configuration from '../../utils/Configuration';
import {chartTypes} from '../../utils/chartDefinitions.js';

const props = defineProps(['appData', 'componentPlaceHolderData'])
const emit = defineEmits(['closeDialog'])

console.log( "props.componentPlaceHolderData: ", props.componentPlaceHolderData)
const isPro = Configuration.IS_PRO;

function showUpgradeLicenseDialog(showDialog=true){
    window.showUpgradeLicenseDialog(showDialog);
}

function getChartTemplate( chartId ){
    console.log( 'chartTypes', chartTypes );
    let chart = chartTypes.find( (chart)=>{ return chart.id == chartId } );
    if( !chart ){
        console.error("Could not find chart with id: " + chartId);
        return "";
    }
    return chart.traceTemplate;

}
const getBlockIconPath = function(block)  {   
    let iconFileName;
    if( block.icon ){
        iconFileName = block.icon;
    }else{
        iconFileName = block.type.split("q-").join("")+'.png';
    }
    const result = 'img/block_icons/'+iconFileName
    return result;
}

function selectCompProp(comp, prop){
    console.log( 'selectCompProp(comp, prop', comp, prop );
    
    window.logUserEvent( "component::addFromBinding", { 
        component: comp.type, 
        bindingDataType: props.componentPlaceHolderData.datatype
    });

    let newElement = new DOMParser().parseFromString(comp.content, 'text/html').body.childNodes[0];
    
    // prepend colon if prop is not v-model
    let propName = prop; // == 'v-model' ? prop : ':'+prop;
    let finalBindingName = props.componentPlaceHolderData.bindingname;
    
    // If binding is dataframe, pick first column
    if( props.componentPlaceHolderData.datatype == "DataFrames.DataFrame" ){
        let firstColumn = getDataFrameColumn( props.componentPlaceHolderData.bindingname );
        finalBindingName += '.' + firstColumn;
        // Tag as interpolated value
        finalBindingName ='$_{' + finalBindingName + '}';
        // get trace template
        let template = getChartTemplate( comp.label );
        // inject binding
        if( template.x != null ) template.x = finalBindingName;
        if( template.y != null ) template.y = finalBindingName;
        if( template.z != null ) template.z = finalBindingName;
        if( template.labels != null ) template.labels = finalBindingName;
        if( template.values != null ) template.values = finalBindingName;
        

        // Encapsulate in trace object
        console.log( 'traceTemplate: ', template );
        //finalBindingName = `[{"x": "${finalBindingName}", "y": "${finalBindingName}", "type": "scatter", "name": "Trace" }]`;
        finalBindingName = `[${JSON.stringify(template)}]`;
        // Encode
        finalBindingName = btoa( finalBindingName );
        // Remove the constant data
        newElement.removeAttribute('gb_c__data');
        console.log( 'finalBindingName', finalBindingName );
        newElement.setAttribute( "gb_c__" + propName, finalBindingName );
    }else if( props.componentPlaceHolderData.datatype == "DataTable.DataFrames.DataFrame" ){
        // Remove the constant data
        newElement.removeAttribute('gb_c__data');
        // Add the dynamic data binding using its columns property
        finalBindingName += '.data';
        newElement.setAttribute( "gb_b__" + propName, finalBindingName );
        newElement.setAttribute( "gb_b__columns", props.componentPlaceHolderData.bindingname + ".columns" );
    }else{
        newElement.setAttribute( "gb_b__" + propName, finalBindingName );
    }
    let newElementHtml = newElement.outerHTML;
    editor.getSelected().replaceWith( newElementHtml );
    closeDialog(false);
}

function closeDialog(removeSelected){
    emit('closeDialog', removeSelected);
}

// ------------------------------------------


// ====================== COMPUTED ====================== 

function getDataFrameColumn( dfName ) {
    let dsFields = props.appData.pages[Configuration.currentPageIndex].model.fields.filter( (field)=>{ 
        return field.name == dfName;
    } );
    if( dsFields.length == 0 ){
        console.error("Could not find dataframe with name: " + dfName);
        return "";
    }
    if( !dsFields[0].columns || dsFields[0].columns.length == 0 ){
        console.error( "Dataframe " + dfName + " has no columns" );
        return "";
    }
    return dsFields[0].columns[0];
}

</script>


