function getBlockDefinitions(){
  return _blockDefinitions;
}


function loadBlockDefinitions(readyCallback){
  // Load dynamically and call callback function when ready
  axios.get('data/blockDefinitions.json')
    .then( result => {
      _blockDefinitions = result.data;
      parseDefinitionsSource();
      addSpecialBlocks();
      readyCallback(_blockDefinitions);    
    } )
    .catch( (error) => {
      console.error( "Error loading block definitions: ", error );
    });
}


function parseDefinitionsSource(){
  // Filter-out non-enabled blocks
  _blockDefinitions = _blockDefinitions.filter( (block) => block.enabled );
  
  _blockDefinitions.forEach( (block) => {
    // Remove v-model as we'll use model-value instead
    let indexOfVmodel = block.properties.findIndex( (prop) => prop.name == "v-model" );
    if( indexOfVmodel > -1 ){
      block.properties.splice(indexOfVmodel, 1);
    }
    
    // rename model-value to be Data Binding (v-model)
    let indexOfModelValue = block.properties.findIndex( (prop) => prop.name == "model-value" );
    if( indexOfModelValue > -1 ){
      block.properties[indexOfModelValue].name = "v-model";
      block.properties[indexOfModelValue].label = "Data Binding";
    }

    // Remove "-" values from bindings as they are not valid values
    if( block.content.indexOf('v-on:click') > -1 || block.content.indexOf('v-model') > -1 ){
      block.content = block.content.replace( `v-on:click="-"`, `v-on:click=""`);
      block.content = block.content.replace( `v-model="-"`, `v-model=""`);
    }

    // Add "v-if" and "v-for" to the properties
    block.properties.push( { name: 'v-if', label: 'Condition (if)', type:'Boolean', uitype:'String', desc: `Defines a conditional which, when true, will show the element in the document.\nYou can use JS code, i.e. "value > 10"` } );
    block.properties.push( { name: 'v-else-if', label: 'Condition (else-if)', type:'Boolean', uitype:'String', desc: `Defines a conditional which, when true, will show the element in the document.\nYou can use JS code, i.e. "value > 10"` } );
    block.properties.push( { name: 'v-else', label: 'Condition (else)', type:'Boolean', uitype:'String', desc: `Defines a conditional which, when previous if/else-if statements are false, will show the element in the document."` } );
    block.properties.push( { name: 'v-for', label: 'Loop (for)', type:'Array', desc: `Sets the element to iterate over a list of values, producing multiple instances.\nYou can declare iterator values pointing to the list elements and indexes, and use them in other props.\nI.e.: \n- item in list\n- (item, index) in list` } );

    let blockProps = block.properties;
    blockProps.forEach( (prop) => {
      for( let propAttributeName in prop ){
        let propAttrValue = prop[propAttributeName];

        // Replace interpolated values (i.e. ${supportedIcons})) with the actual value
        if( typeof propAttrValue == "string" && propAttrValue.indexOf('${') == 0 ){
          let variableName = propAttrValue.slice(2,-1);
          let variableValue = injectableVariables[variableName];
          prop[propAttributeName] = variableValue;
        }

        // In case of multiple types for a prop, leave only the first one
        // To-do: support for multiple types
        if( Array.isArray(propAttrValue) ){
          prop[propAttributeName] = propAttrValue[0];
        }
      }
    });
  });
}


function addSpecialBlocks(){    
  // TempPlaceholder for dragged-bindings
  _blockDefinitions.push({
    type: "tempplaceholder", label: "Temp PlaceHolder", tagName: "TEMPPLACEHOLDER", 
    content: "<tempplaceholder>TEMP Place holder</tempplaceholder>", 
    properties: [], 
    droppable: false,     editable: false,     draggable: true,
  } );
  
  // Generic Div
  /* _blockDefinitions.push({
    type: "looper", label: "Looper", tagName: "looper", 
    content: "<looper>Generic looper</looper>", 
    properties: [ { name: 'v-if', label: 'Condition (if)', type:'String' }, { name: 'v-for', label: 'Loop (for)', type:'String' } ], 
    //hideFromLibrary: true,
    droppable: true,     editable: false,     draggable: true,
  } ); */

  // ========================================================
  //                    Plotly charts dynamic
  // ========================================================
  chartTypes.forEach( (chartTypeDef) => {
    let encodedData = btoa( JSON.stringify( [chartTypeDef.traceTemplate] ) );
    let traceLayout = chartTypeDef.traceLayout;
    let encodedLayout = traceLayout ? btoa( JSON.stringify( traceLayout ) ) : "eyJ0aXRsZSI6Ik15IENoYXJ0In0=";
    _blockDefinitions.push({
      type: "plotly", label: chartTypeDef.id, tagName: "PLOTLY", category: "Charts", 
      content: `<plotly gb_c__data="${encodedData}" gb_c__layout="${encodedLayout}" gb_c__config="e30="></plotly>`, 
      renderContent: `<img src="img/block_icons/areaChart.png" />`,  
      icon: chartTypeDef.icon+'.png', proRequired: chartTypeDef.proRequired,
      
      properties: [
        { label: 'Data', name: 'data', type:'PlotlyBase.GenericTrace' }, 
        { label: 'Layout', name: 'layout', type:'PlotlyBase.Layout' }, 
        { label: 'Config', name: 'config', type:'Object' }, 
        ], 
    });
  });
}

export { getBlockDefinitions, loadBlockDefinitions };