import axios from "axios";
import { useToast } from "vue-toastification";
import Configuration from "./Configuration";
const toast = useToast();




export default function activateInterceptors(){
    // Add a request interceptor
    axios.interceptors.request.use(function (config) {
        // Remove gb-sessionid header if url begins with Configuration.ask_ai_url
        if(config.url.startsWith(Configuration.ask_ai_url)){
            delete config.headers['gb-sessionid'];
        }
        return config;
    }, function (error) {
        // Do something with request error
        console.error( `Axios interceptor REQUEST ERROR: `, error );
        return Promise.reject(error);
    });

    // Add a response interceptor
    axios.interceptors.response.use(function (response) {
        // Any status code that lie within the range of 2xx cause this function to trigger
        return response;
    }, function (error) {
        console.error( `Axios interceptor RESPONSE ERROR: `, error );
        // Any status codes that falls outside the range of 2xx cause this function to trigger
        if( error.response.status == 429 || error.response.status == 402 ){
            toast( error.response.message || "There was a problem with your subscription. Please try again later or contact support.");
        }
        return Promise.reject(error);
    });
}