<template>
    <div>
        <div v-if="!component" class="nothingSelected">
            <i>Nothing selected</i>
        </div>

        <div v-if="component && !licenseAllowsComponent(component)" class="notProUI">
            This component requires a Pro license.<br/>
            Upgrade to Pro and start using it now!<br/> <br/>
            <vscode-button @click="showUpgradeLicenseDialog" style="margin-right: 3px;">Upgrade</vscode-button> 
        </div>

        <div v-if="component && licenseAllowsComponent(component) && component.attributes.type != 'tempplaceholder' && component.attributes.type != 'plotly'">
            <div style="user-select:none;">
                <div 
                    v-for="attributeObject in getCleanAttributesList()" :key="attributeObject.name" 
                    class="propRow" 
                    >
                    <div class="removeBtnCnt" style="width:20px;">
                        <span v-if="attributeObject.def?.mandatory != true && attributeObject.name!='gb_b__v-model'" @click="removeAttribute(attributeObject)" title="Remove Property" class="codicon codicon-settings">
                            <i aria-hidden="true" role="img" class="q-icon notranslate material-icons" style="font-size: 14px; padding: 0px;">delete</i>
                        </span>
                    </div>
                    <div class="propRowLabel" :title="attributeObject.def.name + ': ' + attributeObject.def.desc">{{ attributeObject.def.label }}: </div>
                    <div v-if="!attributeIsBinding(attributeObject.name)" class="propRowControl">
                        <!-- Dropdown (generic, with predefined ooptions) -->
                        <vscode-dropdown 
                            v-if="attributeObject.def.uitype=='select'"
                            style="width: 100%; min-width: unset;" 
                            :value="component.attributes.attributes[ attributeObject.name ]"
                            @input="(event) => component.attributes.attributes[ attributeObject.name ] = event.target.value" 
                            @change="submitChanges">
                            <span slot="selected-value" class="codicon codicon-settings">
                                {{ component.attributes.attributes[ attributeObject.name ] }}
                            </span>
                            <vscode-option :value="option" v-for="option in attributeObject.def.options" style="padding: 5px;">
                                {{ option }}</vscode-option>
                        </vscode-dropdown>

                        <!-- Dropdown for icons (uitype == 'enum_icons' in blockDefinitions) -->
                        <vscode-dropdown 
                            v-else-if="attributeObject.def.uitype=='enum_icons'"
                            style="width: 100%; min-width: unset;" 
                            :value="component.attributes.attributes[ attributeObject.name ]"
                            @input="(event) => component.attributes.attributes[ attributeObject.name ] = event.target.value" 
                            @change="submitChanges">
                            <span slot="selected-value" class="codicon codicon-settings">
                                <i aria-hidden="true" role="img" class="q-icon notranslate material-icons" style="font-size: 16px; padding: 5px;">{{component.attributes.attributes[ attributeObject.name ]}}</i> {{ component.attributes.attributes[ attributeObject.name ] }}
                            </span>
                            <vscode-option :value="option" v-for="option in attributeObject.def.options" style="padding: 5px;">
                                <i aria-hidden="true" role="img" class="q-icon notranslate material-icons" style="font-size: 16px; ">{{option}}</i> {{ option }}</vscode-option>
                        </vscode-dropdown>

                        <!-- Dropdown for icons (uitype == 'enum_colors' in blockDefinitions) -->
                        <vscode-dropdown 
                            v-else-if="attributeObject.def.uitype=='enum_colors'"
                            style="width: 100%; min-width: unset;" 
                            :value="component.attributes.attributes[ attributeObject.name ]"
                            @input="(event) => component.attributes.attributes[ attributeObject.name ] = event.target.value" 
                            @change="submitChanges">
                            <!-- this is to set the selected value region (component view when not expanded) -->
                            <span slot="selected-value">
                                <q-icon name="circle" :color="component.attributes.attributes[ attributeObject.name ]" style="margin-right: 5px;" /> 
                                {{ component.attributes.attributes[ attributeObject.name ] }}
                            </span>
                            <!-- this is to set the expanded otions views -->
                            <vscode-option :value="option" v-for="option in attributeObject.def.options" style="padding: 5px;">
                                <q-icon name="circle" :color="option" style="margin-right: 5px;" /> 
                                {{ option }}</vscode-option>
                        </vscode-dropdown>
                        <input 
                            :attrname="attributeObject.name"  type="text"
                            v-else-if="attributeObject.def.uitype=='String' || (attributeObject.def.type=='String' && attributeObject.cleanName.indexOf('color') == -1) " 
                            @drop.prevent="onDrop" @dragenter.prevent @dragover.prevent class="propInput" @change="submitChanges" 
                            v-model="component.attributes.attributes[ attributeObject.name ]" 
                            style="width: 220px%;" 
                        />
                        <input 
                            :attrname="attributeObject.name"  type="text"
                            v-else-if="attributeObject.def.type=='Array'" 
                            @drop.prevent="onDrop" @dragenter.prevent @dragover.prevent class="propInput" @change="submitChanges" 
                            v-model="component.attributes.attributes[ attributeObject.name ]" 
                            style="width: 220px%;" 
                        />
                        <input 
                            :attrname="attributeObject.name"  type="text"
                            v-else-if="attributeObject.def.type=='Function'" 
                            @drop.prevent="onDrop" @dragenter.prevent @dragover.prevent class="propInput" @change="submitChanges" 
                            v-model="component.attributes.attributes[ attributeObject.name ]" 
                            style="width: 220px%;" 
                        />
                        <input 
                            :attrname="attributeObject.name"  type="color"
                            v-else-if="attributeObject.def.type=='String' && attributeObject.name.indexOf('color') > -1 " 
                            @drop.prevent="onDrop" @dragenter.prevent @dragover.prevent class="propInput" @change="submitChanges" 
                            v-model="component.attributes.attributes[ attributeObject.name ]" 
                            style="padding: 0px 2px; height: 21px;" 
                        />
                        <input 
                            :attrname="attributeObject.name"  type="number"
                            v-else-if="attributeObject.def.type=='Number'" 
                            @drop.prevent="onDrop" @dragenter.prevent @dragover.prevent class="propInput" @change="submitChanges" 
                            v-model="component.attributes.attributes[ attributeObject.name ]"                         
                        />
                        <input 
                            :attrname="attributeObject.name"  type="checkbox"
                            v-else-if="attributeObject.def.type=='Boolean'" 
                            @drop.prevent="onDrop" @dragenter.prevent @dragover.prevent class="propInput" @change="submitChanges"
                            v-model="component.attributes.attributes[ attributeObject.name ]" 
                            style="width: auto;"                        
                        />
                    </div>
                    <div v-if="attributeIsBinding(attributeObject.name)" class="propRowControl">
                        <select class="propInput propInputDynamic" v-model="component.attributes.attributes[ attributeObject.name ]" name="cars" id="cars" @change="submitChanges" 
                        @drop.prevent="onDrop" @dragenter.prevent @dragover.prevent :attrname="attributeObject.name">
                            <option v-for="binding in filteredModelFields(attributeObject, component)" :key="'binding_'+binding.name" :value="binding.name">{{binding.label ? binding.label : binding.name }}</option>
                        </select>
                    </div>
                    <div v-if="attributeObject.name!='gb_b__v-model' &&attributeObject.name!='gb_c__v-for'" @click="toggleDynamicAttribute(attributeObject.name, attributeObject.value )" class="propRowDynamicSwitch">
                        <img src="img/icons/binding_on.png" style="height: 16px;" v-if="attributeIsBinding(attributeObject.name)" draggable="false" />
                        <img src="img/icons/binding_off.png" style="height: 16px;" v-if="!attributeIsBinding(attributeObject.name)" draggable="false" />
                    </div>
                    <div v-else class="propRowDynamicSwitch" draggable="false" style="cursor: auto;"></div>
                </div>

                <!-- suggested attributes -->
                <div class="suggestedPropertiesContainer" style="margin: 20px 0px 0px 0px;" v-if="getSuggestedProperties().length>0">
                    <div class="suggestedPropertiesTitle" style="margin-bottom:10px; font-weight: bold; font-size: 0.9em;">Suggested Properties:</div>
                    <div 
                        v-for="attributeDefObject in getSuggestedProperties()" :key="attributeDefObject.name" class="propRow" >
                        <div class="propRowLabel" style="font-weight: bold; text-align: left:">{{ attributeDefObject.label }}: </div>
                        <div class="propRowControl" style="text-align: left; font-size: 0.8em; user-select: none;">
                            {{ attributeDefObject.desc }}
                        </div>
                        <div class="propRowDynamicSwitch" draggable="false" title="Add property" @click="addProperty(attributeDefObject)">
                            <i class="fa fa-plus"></i>
                        </div>
                    </div>
                </div>
            </div>
            <br/>
            <vscode-button v-if="!showAddProperty && component" @click="showAddPropertyList">Add Property</vscode-button>
            <div v-if="!component" style="font-size: 0.8em;"><i>Nothing selected</i></div>

            <AddPropertyDialog v-if="showAddProperty" :propslist="availableProperties" @propAdded="addProperty" @close="hideAddProperty">
            </AddPropertyDialog>

        </div>
        <ChartProperties ref="chartPropertiesPanel" :appData="appData" :selectedComponent="selectedComponent" style="height: 100%;"/>
    </div>
</template>



<script setup>

import { ref, onMounted, computed } from 'vue'
import AddPropertyDialog from './properties/AddPropertyDialog.vue';
import ChartProperties from '../ChartProperties.vue'
//import blockDefinitions from '../../utils/blockDefinitions';
import HtmlTemplateParser from '../../utils/HtmlTemplateParser';
import _ from 'lodash'
import Configuration from '../../utils/Configuration';

// Component props (set from outside)
const props = defineProps(['title', 'appData', 'selectedComponent', 'parentPanel']);

function showUpgradeLicenseDialog(){
    window.showUpgradeLicenseDialog(false);
}

function licenseAllowsComponent( component ){
    if( Configuration.IS_PRO ){
        return true;
    }
    let componentType = component.attributes.tagName;
    let componentDefinition = getBlockDefinition( componentType );
    let requiresPro = componentDefinition?.proRequired;
    if( requiresPro ){
        return false;
    }else{
        return true;
    }
}

const chartPropertiesPanel = ref(null);

/*  ======================================================================================
                                    COMPONENT VARIABLES
    ====================================================================================== */

// Component variables
let component = ref(null);
let showAddProperty = ref(false);
let modelFields = ref(props.appData.pages[Configuration.currentPageIndex].model.fields);
let modelFieldsByName = {};
    modelFields.value.forEach( field => {
        modelFieldsByName[field.name] = field;
    });

// Dictionary for easy access to blockDefinitions
const blockDefinitionsByType = blockDefinitions.reduce((acc, block) => {
    acc[block.type] = block;
    return acc;
}, {});



/*  ======================================================================================
                                    COMPONENT METHODS
    ====================================================================================== */

/**
 * This function takes an attribute name as input and returns a cleaned version of the name.
 * If the attribute name starts with "gb_c__" or "gb_b__", it removes the prefix.
 * If the attribute name doesn't start with these prefixes, it returns the attribute name as is.
 * @param {string} attributeName - The name of the attribute to clean.
 * @return {string} The cleaned attribute name.
 */
function getAttributeCleanRealName( attributeName ){
    try{
        if( attributeName.indexOf("gb_c__") == 0 || attributeName.indexOf("gb_b__") == 0 ){
            return attributeName.substring(6);
        }
        return attributeName;
    }catch(error){
        console.error( 'Error in getAttributeCleanRealName: ', attributeName, error );
    }
}



/**
 * Removes an attribute from the selected component.
 * @param {Object} attributeObject 
 */
function removeAttribute(attributeObject ){
    component.value.removeAttributes( [attributeObject.name]);
    submitChanges();
    window.logUserEvent( "component-property::removed", { 
        component: component.value.attributes.tagName, 
        property: getAttributeCleanRealName( attributeObject.name ) 
    });

}



/**
* Returns a list of suggested properties for the selected component.
* The suggested props for each component are defined in the blockDefinitions list (attribute "suggest" = true).
* If the property is already present in the component attributes, it is not suggested.
* @return {Array} An array of suggested properties.
*/
function getSuggestedProperties(){
    let componentDefinition = getBlockDefinition( component.value.attributes.tagName );
    if( !componentDefinition ){
        return [];
    }
    let currentAttributesCleanNames = getCleanAttributesList().map( att => att.cleanName );
    let suggestedProperties = componentDefinition.properties.filter( propDef => {
        return propDef.suggested == true;
    })
    let suggestedPropertiesNotUsed = suggestedProperties.filter( propDef => {
        return currentAttributesCleanNames.indexOf( propDef.name ) == -1;
    });

    return suggestedPropertiesNotUsed;
}




    
/** Returns a list of model fields filtered by type.
 * @param {Object} attributeObject - An object containing the attribute definition.
 * @return {Array} An array of model fields.
 */
function filteredModelFields( attributeObject, component){
    let results = [];
   

    // parse v-for if present
    //debugger;
    if( component?.view?.el ){
        let componentAndAncestorsVforVariableNames = HtmlTemplateParser.extractVforVariablesAncestors( component.view.el );
        componentAndAncestorsVforVariableNames.forEach( vforVar => {
            results.push( { name: vforVar, label: vforVar+' *' } );
        })
    }
    
    /* if( component && component.attributes.attributes['gb_c__v-for'] ){
        let vFor = HtmlTemplateParser.parseVFor( component.attributes.attributes['gb_c__v-for'] );
        if( vFor ){
            let variables = vFor.variables;
            variables.forEach( variable => {
                results.push( { name: variable, label: variable+'**' } );
            })
        }
    } */

    modelFields.value.forEach( field =>{ 
        if( attributeObject.def.type == "Any" || field.type == attributeObject.def.type ){
            results.push( field );
        }else if( field.type == "DataTable.DataFrames.DataFrame"){
            // DataTables expected to have props
            let props = field.props;
            if( props ){
                props.forEach( (prop)=>{
                    results.push( { name: field.name + "." + prop } );
                } )
            }
        }
    } );
    return results;
};



/**
 * Returns a list of attributes for the selected component, 
 * including the name of the attribute, the cleaned name and the value, as well as the definition of the attribute.
 * @return {Array} An array of attributes.
 */
function getCleanAttributesList(){
    const elementAttributes = component.value.attributes.attributes;
    let result = [];
    let attrsForVuecomponent = {};
    for( let p in elementAttributes ){
        if( p.startsWith('gb_c__') || p.startsWith('gb_b__') ){
            let cleanName = getAttributeCleanRealName(p).toLowerCase(); // p.toLowerCase();            
            if( cleanName != 'id' && cleanName != 'charttype' && cleanName != 'class' ){
                let def = getBlockPropertyDefinition(cleanName);
                if( !def ){
                    console.error("Definition not found for: ", p, cleanName, elementAttributes[p])
                }
                result.push( { name:p, cleanName, value: elementAttributes[p], def:def } );
                attrsForVuecomponent[p] = elementAttributes[p];
            }
        }
    }
    
    if( props.selectedComponent.view.el.vueApp ){
        props.selectedComponent.view.el.vueApp.updateAttributes( attrsForVuecomponent )
    }

    result = _.sortBy( result, 'cleanName' );    
    return result;
}



/** 
 * Returns the definition of a property for the selected component.
 */
function getBlockPropertyDefinition( propertyName ){
    // Usually we take the "type" as uid
    let realType = component.value.attributes.type;
    // but native-text elements (p, h1, h2, etc...) have their type set to "text"
    // so we need to infer the type from the tagName
    if( realType == "text" ){
        realType = component.value.attributes.tagName;
    }
    let blockDefinition = getBlockDefinition( realType );
    if( !blockDefinition ){
        return null;
    }
    let propertyDefinition = blockDefinition.properties.find( prop => prop.name == propertyName );
    return propertyDefinition;
}



/** 
 * Returns true if the attribute is a dynamic binding (starts with "gb_b__").
 */
function attributeIsBinding(attributeName ){
    let isDynamic = attributeName.indexOf('gb_b_') == 0;
    return isDynamic;
}



/** 
 * Event handler for the drop event when user drops a binding on top of a property.
 * It will swtich to "binding" mode if it's not already so, and then assign the binding to the property.
 */
function onDrop( event ){
    let dragTransferData = JSON.parse( event.dataTransfer.getData("text") );
    let attrname = event.target.getAttribute('attrname');
    if( attrname.indexOf('gb_c__') == 0 ){
        toggleDynamicAttribute( attrname, dragTransferData.bindingname );
    }else{
        let newAttrs = {};
        newAttrs[attrname] = dragTransferData.bindingname;
        component.value.addAttributes( newAttrs );
    }
};



/** 
 * Shows the "Add Property" dialog.
 */
function showAddPropertyList(){
    showAddProperty.value = true;
}

/** 
 * Hides the "Add Property" dialog.
 */
function hideAddProperty(){
    showAddProperty.value = false;
}



/**
 * Adds a property to the selected component.
 * @param {Object} prop 
 */
function addProperty(prop){
    let defaultValuesByType = {
        Boolean: true,
        String: "", 
        Number: 0, 
        Array: [], 
        Function: ""
    }
    let attrsObject = {};
    let gbName = "gb_c__"+prop.name;
    let defaultValue = defaultValuesByType[prop.type] != null ? defaultValuesByType[prop.type] : "";
    attrsObject[gbName] = defaultValue;
    component.value.addAttributes(attrsObject);
    submitChanges();

    window.logUserEvent( "component-property::added", { 
        component: component.value.attributes.tagName, 
        property: getAttributeCleanRealName( prop.name ) 
    });
}



/**
 * Returns the definition of a block (component) by type.
 * @param {string} type - The type of the block (component).
 * @return {Object} The definition of the block (component).
 */
function getBlockDefinition( type ){
    let componentDefinition = blockDefinitionsByType[type];
    return componentDefinition;
}



/**
 * Returns a list of properties that can be added to the selected component.
 * It excludes the ones already present in the component.
 */
const availableProperties = computed( ()=>{
    let componentDefinition = getBlockDefinition( component.value.attributes.tagName );
    if( !componentDefinition ){
        return [];
    }
    
    let definitionProperties = componentDefinition.properties;
    let currentAttributes = component.value.getAttributes();
    let currentAttributesClean = [];
    for( let p in currentAttributes ){
        let cleanName = getAttributeCleanRealName(p);
        currentAttributesClean.push( cleanName );
    }
    let filtered = [];
    definitionProperties.forEach( prop => {
        let cleanName = getAttributeCleanRealName(prop.name);
        if( currentAttributesClean.indexOf(cleanName) == -1 ){
            filtered.push( prop );
        }
    })
    return filtered;
} );



/**
 * Submits the changes to the selected component.
 * This is needed so that the component in the canvas is updated with the new attributes.
 */
function submitChanges(){
    //setTimeout( ()=>{
            // before parsing the html, we need to make sure we handle booleans properly
            let componentAttributes = component.value.attributes.attributes;
            for( let attName in componentAttributes ){
                if( componentAttributes[attName] === true ){
                    componentAttributes[attName] = 'true';
                }
                if( componentAttributes[attName] === false ){
                    componentAttributes[attName] = 'false';
                }
            }
            let newHtml = selectedComponent.toHTML();
            editor.getSelected().replaceWith( newHtml );
            
            
            let clonedAttributes = {};                    
            let attributeNames = Object.getOwnPropertyNames(componentAttributes);
            attributeNames.forEach( (attrName)=>{
                let attributeValue;
                if( componentAttributes[attrName] != undefined && componentAttributes[attrName].nodeValue != undefined ){
                    attributeValue = componentAttributes[attrName].nodeValue;
                } else{
                    attributeValue = componentAttributes[attrName];
                }
                clonedAttributes[attrName] = attributeValue;
            })
        props.selectedComponent.setAttributes(clonedAttributes);
            let grapesElementVueApp = props.selectedComponent.view.$el[0].vueApp;
            if( grapesElementVueApp )
                grapesElementVueApp.updateAttributes(clonedAttributes);
    //}, 1000);
}



/**
 * Toggles the attribute mode from constant to binding and viceversa
 * @param {string} key - The name of the attribute.
 * @param {string} value - The value of the attribute.
 */
function toggleDynamicAttribute(key, value){
    let newKey = key.indexOf('gb_b__') == 0 ? key.replace( 'gb_b__', 'gb_c__' ) : key.replace( 'gb_c__', 'gb_b__' );
    component.value.removeAttributes( [key]);
    let newAtts = {};
    // When switching to Binding mode, pick the model variable with the same name set as constant or,
    // If it doesn't exist, pick one of the same type by default
    if( newKey.indexOf('gb_b__') == 0 ){
        if(modelFieldsByName[value] != null )
            newAtts[newKey] = value;
        else{
            let cleanAttributeName = getAttributeCleanRealName(newKey).toLowerCase();
            let def = getBlockPropertyDefinition(cleanAttributeName);

            let attributeObject = { def }
            let sameTypeModelFields = filteredModelFields(attributeObject);
            if( sameTypeModelFields.length > 0 )
                newAtts[newKey] = sameTypeModelFields[0].name;
            else
                newAtts[newKey] = "Not Set";
        }
    }else{
        newAtts[newKey] = value;
    }

    component.value.addAttributes(newAtts);
    submitChanges();
}

/**
 * Sets the selected component.
 * @param {Object} newComponent 
 */
function setSelectedComponent(newComponent){
    if( props.parentPanel){
        if( newComponent )
            props.parentPanel.expand(true);
        else
            props.parentPanel.expand(false);
    }
    component.value = newComponent;
    if( newComponent && newComponent.attributes.type == 'plotly' ){
        chartPropertiesPanel.value.setSelectedComponent( newComponent );
    }
    /* if( chartPropertiesPanel.value){
      chartPropertiesPanel.value.setSelectedComponent(com);
    } */
}



/*  ======================================================================================
                                    COMPONENT LIFECYCLE HOOKS
    ====================================================================================== */
onMounted( ()=>{
    setSelectedComponent( window.selectedComponent );
});



/*  ======================================================================================
                                    COMPONENT EXPORTS
    ====================================================================================== */
defineExpose({
    setSelectedComponent, chartPropertiesPanel
})
</script>



<style lang="scss" scoped>
@import "../../styles/globalStyles.scss";

.propRow{
    position: relative;
    border-bottom: solid 1px var(--checkbox-background);
    display: flex;
    flex-direction: row;
    gap: 10px;
    padding: 5px 0px 5px;
}
.propRowLabel{
    width: 35%;
    font-size: 0.9em;
    display: flex;
    justify-content: right;
    align-items: center;
    text-transform: capitalize;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    user-select: none;
}
.propRowControl{
    width: 60%;
    display: flex;
    justify-content: left;
}

.propRowDynamicSwitch{
    width: 5%;
    display: flex;
    justify-content: left;
    align-items: center;
    cursor: pointer;
    opacity:0.7;
}

.propRowDynamicSwitch:hover{
    opacity:1;
}

.addPropIcon{
    font-size: 1.2em;
    opacity: 0;
}


.propInput{
    padding: 1px 10px;
    border: solid 1px #999;
    border-radius: 3px;
    box-sizing: border-box;
    width: 100%;
    color: #000000;
}

.propInputDynamic{
    background-color: #fff1cd;
    border: solid 1px #d8b252;
    color: #916a09;
}

.removeBtnCnt{
    opacity: 0;
    cursor: pointer;
}
.propRow:hover .removeBtnCnt{
    opacity: 0.5;
}
.removeBtnCnt:hover{
    opacity: 1 !important;
}
</style>