

function registerPlotlyComponent(editor){
    
    editor.DomComponents.addType('plotly', {

        isComponent: function (el) {
        if (el.tagName === 'PLOTLY')
            return {type: 'plotly', name:'Plotly'};
        },
        model: 
        {
            defaults: {
                removable: true,
                draggable: true,
                droppable: "tempplaceholder",
                badgable: true,
                stylable: true,
                highlightable: true,
                copyable: false,
                resizable: false,
                editable: false,
                hoverable: true,
                traits: [ ],
                script: function(){    
                    var serverData = window.parent.sharedData.externalData;
                    
                    let uniqueId = this.attributes.id.nodeValue;

                    this.onExternalDataChanged = (event)=>{
                        serverData = event.detail;
                        mappedAttributes = getMappedAttributes(this.attributes);
                        this.vueApp.updateServerData(serverData);
                
                    }

                    window.parent.addCanvasComponent( uniqueId, this );
                    //window.parent.addEventListener('sharedDataChanged', this.onExternalDataChanged);
                    
                    
                    
                    /**
                     * This function takes in an object of attributes and maps them to a new object with modified attribute names.
                     * If the attribute name contains a colon, it is removed. If the attribute name is 'data', its value is parsed as JSON.
                     * The resulting object is returned.
                     */
                    // To-do: these functions should be moved to a utility file.
                    // Be careful because it is duplicated in ChartProperties.vue
                    // HAven't done so yet because the scope of the grapes component instance is isolated and can't access outside functions
                    function getMappedAttributes(attributesIn){
                        let mappedAttributes = {};                    
                        let attributeNames = Object.getOwnPropertyNames(attributesIn);
                        attributeNames.forEach( (attrName)=>{
                            let cleanAttributeName = attrName.replace("gb_c__", "");
                            let attributeValue = attributesIn[attrName].nodeValue !== undefined ? attributesIn[attrName].nodeValue: attributesIn[attrName];
                            if( ['data', 'layout', 'config'].includes(cleanAttributeName) ){
                                attributeValue = atob(attributeValue)
                                if( serverData[attributeValue] !== undefined ){
                                    mappedAttributes[':'+cleanAttributeName] = serverData[attributeValue];
                                } else {
                                    mappedAttributes[':'+cleanAttributeName] = JSON.parse(attributeValue);
                                }
                            }
                        })
                        return mappedAttributes;
                    }

                    function replaceReferencedBindingsToAttributeData(obj) {
                        for (let key in obj) {
                          if (typeof obj[key] === 'object' && obj[key] !== null) {
                            // Recursively process nested objects
                            replaceReferencedBindingsToAttributeData(obj[key]);
                          } else if (typeof obj[key] === 'string') {
                            // Check if string value contains "$_{xxxxx}"
                            const match = obj[key].match(/\$_{(.+?)}/);
                            if (match && match[1]) {
                              const propToReplace = match[1];
                              // Replace the value with the value from myStore
                              obj[key] = getNestedProperty(serverData, propToReplace);
                            }
                          }
                        }
                    }

                    function getNestedProperty(obj, propName) {
                        const propParts = propName.split('.');
                        for (let i = 0; i < propParts.length; i++) {
                            if (!obj || !obj.hasOwnProperty(propParts[i])) {
                                return undefined;
                            }
                            obj = obj[propParts[i]];
                        }
                        return obj;
                    }

                    let mappedAttributes = getMappedAttributes(this.attributes);


                    this.vueApp = new Vue({
                        el: '#vueinternal_'+uniqueId,
                        //components: { QBtn:Quasar.}, 
                        data: function(){ 


                            let parsedData = mappedAttributes[':data'];
                            replaceReferencedBindingsToAttributeData(parsedData);
                            let parsedLayout = mappedAttributes[':layout'] || {};
                            replaceReferencedBindingsToAttributeData(parsedLayout);
                            let parsedConfig = mappedAttributes[':config'] || {};
                            replaceReferencedBindingsToAttributeData(parsedConfig);
                            
                            return {
                                
                                message:"TESTING_ID",
                                attributes: mappedAttributes,  
                                serverData: serverData,
                                parsedData: parsedData, 
                                parsedLayout: parsedLayout, 
                                parsedConfig: parsedConfig
                            }
                        }, 
                        methods: {
                            updateServerData(newServerData){
                                this.serverData = newServerData;
                                let parsedData = mappedAttributes[':data'];
                                replaceReferencedBindingsToAttributeData(parsedData);
                                this.parsedData = parsedData;
                                let parsedLayout = mappedAttributes[':layout'] || {};
                                replaceReferencedBindingsToAttributeData(parsedLayout);
                                this.parsedLayout = parsedLayout;
                                let parsedConfig = mappedAttributes[':config'] || {};
                                replaceReferencedBindingsToAttributeData(parsedConfig);
                                this.parsedConfig = parsedConfig;
                            }, 
                            updateAttributes(attrs){                                
                                this.attributes = getMappedAttributes(attrs);
                                mappedAttributes = this.attributes;
                                this.updateServerData(this.serverData)
                            }, 

                        }, 
                        mounted() {
                        }
                    });

                }
            },
            handleAttrChange() {
            },
            render: function () {
                this.view.onRender();
            },
            init() {
                this.listenTo(this, 'change:attributes', this.onAttributeChange);

            },
            removed() {
                window.parent.postMessage( {command:"removeCanvasComponent", uid:this.ccid }, "*" );
                //window.parent.removeEventListener('sharedDataChanged', this.view.$el[0].onExternalDataChanged );

            },
        },
        view: {
            onAttributeChange() {
            }, 
            onRender() {
                const { el, model } = this;
                let uniqueId = el.attributes.id.nodeValue;
                el.grapesModel = model;
                const renderContent = `<div id="vueinternal_${uniqueId}" style="position: relative;">
                    <Plotly ref="childVueInstance" :data="parsedData" :layout="parsedLayout" :config="parsedConfig"></Plotly>
                    <div style="position: absolute; background-color: #ff000000; top: 0px; left: 0px; width: 100%; height: 100%;"></div>
                `;
                el.innerHTML = renderContent;
            }
        }

    });
}

module.exports = { registerPlotlyComponent }