  function generateVolumeTrace() {
    const l = 3;
    let X = [], Y = [], Z = [], vol = [];

    // Create grid points for 3D space, similar to mgrid in Julia
    for (let i = 1; i <= l; i++) {
      for (let j = 1; j <= l; j++) {
        for (let k = 1; k <= l; k++) {
          X.push(i);
          Y.push(j);
          Z.push(k);
          // Initially, set the volume to zero
          vol.push(0);
        }
      }
    }

    for (let n = 0; n < 15; n++) {
      const randomIndex = Math.floor(Math.random() * vol.length);
      vol[randomIndex] = 1;
    }

    vol = vol.map((v, idx) => {
      const neighbors = [
        vol[idx - 1] || 0, vol[idx + 1] || 0, 
        vol[idx - l] || 0, vol[idx + l] || 0, 
        vol[idx - l * l] || 0, vol[idx + l * l] || 0 
      ];
      const sum = neighbors.reduce((a, b) => a + b, 0);
      return (v + sum / neighbors.length) / 2;
    });

    const maxVol = Math.max(...vol);
    vol = vol.map(v => v / maxVol);

    const trace = {
      type: 'volume',
      x: X,
      y: Y,
      z: Z,
      value: vol,
      isomin: 0.1,
      isomax: 0.8,
      opacity: 0.1,
      surface_count: 25
    };
    return trace;
  }

