<template>
  <Main ref="mainController" msg="Genie Builder No-Code Editor"/>
</template>

<script setup>
import { computed, ref, onMounted } from 'vue'
import * as uikit from "@vscode/webview-ui-toolkit";
import { provideVSCodeDesignSystem, vsCodeTextField, vsCodeDropdown, vsCodeOption, vsCodeButton, vsCodeCheckbox } from "@vscode/webview-ui-toolkit";
import Main from './components/Main.vue'

window.uikit = uikit;
window.provideVSCodeDesignSystem = provideVSCodeDesignSystem;

const mainController = ref(null);
provideVSCodeDesignSystem().register(
  vsCodeTextField(), 
  vsCodeDropdown(), 
  vsCodeOption(), 
  vsCodeButton(), 
  vsCodeCheckbox()
);

onMounted( ()=>{
    init();
  } );

  function init(){
    window.mainController = mainController;
  }

/* export default {
  name: 'App',
  components: {
    Main
  }
} */
</script>

