[data-theme="light"]{
  --background: #ffffff !important;
  --foreground: #616161 !important;
  --dropdown-background: #ffffff !important;
  --dropdown-foreground: #616161 !important;
  --input-background: #ffffff !important;
  --input-foreground: #616161 !important;
  --sideBarSectionHeader-foreground: #616161 !important;
  --dropdown-list-max-height: 100px !important;
  --library-icon-filter: invert(0) !important;
  /* --focus-border: #333333 !important; */
  #gjs-clm-tag-label{
    color: var(--background) !important;
  }
}

[data-theme="dark"]{  
  --background: #282a36 !important;
  --foreground: #f8f8f2 !important;
  --dropdown-background: #282a36 !important;
  --dropdown-foreground: #f8f8f2 !important;
  --input-background: #282a36 !important;
  --input-foreground: #f8f8f2 !important;
  --sideBarSectionHeader-foreground: #f8f8f2 !important;
  --dropdown-list-max-height: 100px !important;
  --library-icon-filter: invert(1) !important;
  /* --focus-border: #ffffff !important; */
}

  :root {
    --container-paddding: 20px;
    --input-padding-vertical: 6px;
    --input-padding-horizontal: 4px;
    --input-margin-vertical: 4px;
    --input-margin-horizontal: 0;
  }

  body {
    color: var(--foreground);
    font-size: var(--font-size);
    font-weight: var(--font-weight);
    font-family: var(--font-family);
    background-color: var(--editor-background);
    margin: 0px;
  }

  #app {
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
    font-size: 13px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: var(--foreground);
    margin-top: 0px;
    height: 100vh;
  }

  .nothingSelected{
    padding: 10px 20px;
    text-align: center;
    width: 100%;
    opacity: 0.5;
    user-select: none;
  }

  input, select {
    background-color: var(--input-background) !important;
    color: var(--input-foreground) !important;
    border: calc(var(--border-width) * 1px) solid var(--dropdown-border) !important;
    border-radius: calc(var(--corner-radius) * 1px) !important;
  }

  input:focus, input:focus-visible, select:focus, select:focus-visible {
    border: solid 1px var(--focus-border) !important;
    outline: none !important;
  }

  .codicon-chevron-down:before {
    content: '\eab4';
  }

  .notProUI{
    padding: 10px;
    text-align: center;
}

  /* Styles the scrollbar track (the part the thumb slides in) */
  ::-webkit-scrollbar-track {
      background: #f1f1f100; /* Light gray track */
  }

  /* Styles the scrollbar thumb (the draggable part) */
  ::-webkit-scrollbar-thumb {
      background: #cccccc50; /* Dark gray thumb */
  }

  /* Styles the scrollbar thumb on hover */
  ::-webkit-scrollbar-thumb:hover {
      background: #cccccc70; /* Darker gray on hover */
  }

  /* Styles the scrollbar itself (width, color) */
  ::-webkit-scrollbar {
      width: 10px; /* Width of the scrollbar */
  }

  /* For Firefox */
  * {
      scrollbar-width: thin; /* "auto" or "thin" */
      scrollbar-color: #888 #f1f1f1; /* Thumb and track color */
  }


  .splitpanes__splitter {background-color: #cccccc30;position: relative;}
  .splitpanes__splitter:hover {background-color: var(--button-primary-background);  }
  .splitpanes__splitter:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    transition: opacity 0.4s;
    background-color: rgba(127, 127, 127, 0);
    opacity: 0;
    z-index: 1;
  }
  .splitpanes__splitter:hover:before {opacity: 1;}
  .splitpanes--vertical > .splitpanes__splitter:before {left: -2px;right: -2px;height: 100%;}
  .splitpanes--horizontal > .splitpanes__splitter:before {top: -2px;bottom: -2px;width: 100%;}


  .gjs-three-bg {
    background-color: var(--button-primary-background) !important;
  }

  .cke_inner{
    min-width: 530px !important;
  }

  .hljs-number {
    color: #999999 !important;
  }

  .Vue-Toastification__toast{
    background-color: var(--background) !important;
    color: var(--button-primary-text) !important;
    padding: 10px 24px !important;
    min-height: 10px !important;
    border: solid 1px #cccccc90 !important;
    box-shadow: 5px 5px 20px #00000080 !important;
    max-width: 350px !important;
    min-width:300px !important;
  }
  .Vue-Toastification__toast-body{
    font-size: 13px !important
  }
