function onComponentAdded(info) {
  editor.select(info);
  if (info.attributes.tagName == "tempplaceholder") {
    let parent = info.view.$el[0].parentNode;
    let parentBlockType = parent.grapesModel?.attributes.type;
    if (
      parentBlockType == "plotly" &&
      info.attributes.attributes.bindingname &&
      info.attributes.attributes.datatype
    ) {
      emit("bindingDroppedOnComponent", {
        newComponentModel: info, 
        targetModel: parent.grapesModel,
        bindingInfo: info.attributes.attributes,
      });
    } else {
      offerComponents(
        info.attributes.attributes.bindingname,
        info.attributes.attributes.datatype
      );
    }
  }
}

// this version of the function searches only in v-model
function offerComponents(bindingname, datatype) {
  // Find ui components with properties matching the bindingname and datatype
  let defs = blockDefinitions;
  let components = [];
  // Iterate over all components and find those that have a property matching the datatype
  defs.forEach((def) => {
    let componentType = def.type; // this is not datatype, butcomponent type
    let properties = def.properties;
    let matchingdataTypePropertyNames = properties
      .filter((property) => {
        let matchesType = property.type == datatype;
        let isVmodel = property.name == "v-model";
        return matchesType && isVmodel;
      })
      .map((property) => property.name);
    // If there is a property matching the datatype, add the component to the list
    // By now, restricted to v-model
    if (
      (matchingdataTypePropertyNames.length > 0 && matchingdataTypePropertyNames.includes("v-model") )
    ) {
      components.push({
        type: componentType,
        label: def.label,
        content: def.content,
        matchingProperties: matchingdataTypePropertyNames,
        proRequired: def.proRequired,
      });
    }
    // Spcial case for pltly components
    if( datatype == 'DataFrames.DataFrame' && componentType == 'plotly' ){
      components.push({
        type: componentType,
        label: def.label,
        content: def.content,
        icon: def.icon,
        matchingProperties: ['data'],
        proRequired: def.proRequired,
      });
    }else if( datatype == 'DataTable.DataFrames.DataFrame' && componentType == 'q-table' ){
      components.push({
        type: componentType,
        label: def.label,
        content: def.content,
        icon: def.icon,
        matchingProperties: ['data'],
        proRequired: def.proRequired,
      });
    }
  });
  componentPlaceHolderData.value = {
    components: components,
    bindingname: bindingname,
    datatype: datatype,
  };
}



