function getComponentDefinitionByType(type) {
  let defs = blockDefinitions;
  let result = defs.find((def) => def.type.toLowerCase() == type.toLowerCase());
  return result;
}


function updateReactiveModelVariable( key, value){
    reactiveModel.value[key] = value;
    window.sharedData.setExternalData(reactiveModel.value);
}


function sendWsMessage( message, payload={} ){
  let msg = JSON.stringify({
        'channel': Configuration.CHANNEL__,
        'message': message,
        'payload': payload
      });
      ws.send( msg );
}


function subscribeAutoreload( ){
  let msg = JSON.stringify({
        'channel': "autoreload",
        'message': "subscribe",
        'payload': {}
      });
      ws.send( msg );
}


function newSocketConnection() {
  ws = new WebSocket(wsUrl);

    ws.addEventListener('open', event => {
      sendWsMessage( 'subscribe' );    
      subscribeAutoreload();  
    });

    ws.addEventListener('message', event => {
      if( event.data == 'Subscription: OK' ){
        sendWsMessage( 'watchers', 
        { 
          payload:{
            "field": "isready",
            "newval": true,
            "oldval": false,
          }
        });
        return;
      }else if( event.data.indexOf('{') == 0  ){
        let msgObject = JSON.parse( event.data );
        if( msgObject.key != null ){
          updateReactiveModelVariable( msgObject.key, msgObject.value );
        }
      }
    });

    ws.addEventListener('close', event => {
      //console.log( 'newSocketConnection::close', event );
    });

    ws.addEventListener('error', error => {
      //console.log( 'newSocketConnection::error', error );
    });

    return ws
}

// Function used to dynamically load each script to mock the Genie App
function loadScript(src, callback) {
    var script = document.createElement('script');
    script.src = src;
    script.onload = () => callback(src);
    script.onerror = () => console.error(`Error loading script: ${src}`);
    document.head.appendChild(script);
}

function initMockedGenieApp(){
  // Get the app scripts and styles, excluding unneeded ones, 
  // removing ending slash if present
  let scripts = props.appData.pages[Configuration.currentPageIndex].deps.scripts;
  let appBasePath = Configuration.getAppAssetsBaseUrl('http', props.appData.port);
  if( appBasePath[appBasePath.length-1] == '/' ){
    appBasePath = appBasePath.substring(0, appBasePath.length-1);
  }

  // List of app scripts needed to mock the app
  let mockedScriptsToExtract = [
    "assets/js/vue.js", 
    "assets/js/channels.js", 
    "assets/js/stipplecore.js",
    "assets/js/watchers.js",
    /* "assets/js/main_app_varmain_app_reactivemodel.js", 
    "assets/js/main_reactivemodel.js"  */
  ];
  
  // List of scripts, with full paths, to load in the mocked app
  let mockedScripts = [];
  scripts.forEach((script) => {
    let scriptPath = appBasePath + script;
    if ( script.endsWith('_reactivemodel.js') ){
        mockedScripts.push(scriptPath);
    }else{
      mockedScriptsToExtract.forEach((mockedScript) => {
        if (script.indexOf(mockedScript) > -1) {     
          mockedScripts.push(scriptPath);
        }
      });
    }
  });
  
  // Set autorun to false, so that Stipple doesn't start automatically
  window.autorun = false;

  // Dynamically load all scripts required to mock the app
  let loadedScriptsCount = 0;
  mockedScripts.forEach(script => {
      loadScript(script, function() {
          loadedScriptsCount++;
          if (loadedScriptsCount === mockedScripts.length) {
            // When done loading them all, initialize the mocked app
            //console.log('All scripts loaded successfully!');
            initStipple("#Main_App_varMain_App_ReactiveModel");
            // Then updated the shared data with the initial reactive model properties and values
            window.sharedData.setExternalData(window.GENIEMODEL._data);
            // start the WS connection to listen to changes
            newSocketConnection();
            // and finally start the editor, now that the mocked-app, socket and shared data are ready
            initEditor();
          }
      });
  });
}

