    // For composite object, we autoselect the root object after a short delay
    // since this event will also be triggered by its children
    if( com.attributes.attributes.iscomposite=='true' ){
      compositeComponentAddedOrRemoved = true;
      setTimeout(()=>{
        compositeComponentAddedOrRemoved = false;
        onComponentAdded(com);
        emit("componentSelected", com);
        debouncedAutoSave();
      }, 200);
    }else{
      if( !compositeComponentAddedOrRemoved ){
        onComponentAdded(com);
        debouncedAutoSave();
      }
    }
  });
  
  editor.on("component:remove", (com) => {
    if( com.attributes.attributes.iscomposite=='true' ){
      compositeComponentAddedOrRemoved = true;
      setTimeout(()=>{
        compositeComponentAddedOrRemoved = false;
        emit("componentSelected", null);
        debouncedAutoSave();
      }, 200);
    }else{
      if( !compositeComponentAddedOrRemoved ){
        emit("componentSelected", null);
        debouncedAutoSave();
      }
    }    
  });
  
  editor.Commands.add('edit-child-elements', {
      run(editor, sender) {
          //sender && sender.set('active'); // turn off the button
          //console.log('edit-child-elements button clicked');
          mainController.value.startEditingComponentContent()
      }
  });

  editor.on("component:selected", (com) => {
    const selectedComponent = editor.getSelected();
    let isNestable = getComponentDefinitionByType( com.attributes.type )?.nestable;
    if( selectedComponent && isNestable ){
      const toolbar = selectedComponent.get('toolbar') || [];
      let editChildElementsButton = toolbar.find( item => item.command == 'edit-child-elements' );
      if( !editChildElementsButton ){
        toolbar.push({
            attributes: { class: 'editChildElementsIcon', title: 'Edit Child Elements' },
            command: 'edit-child-elements',
        });
      /* console.log( 'toolbar: ', toolbar );
      toolbar.push({
          attributes: { class: 'editChildElementsIcon', title: 'Edit Child Elements', gb-data },
          command: 'edit-child-elements',
      }); */
  
      selectedComponent.set('toolbar', toolbar);
      }
    }

    
    // Skipe event if a composite component was added recenty
    if( compositeComponentAddedOrRemoved ){
      return;
    }else{
      emit("componentSelected", com);
    }
  });



  editor.on("component:update", (com) => { debouncedAutoSave();   });
  editor.on("component:styleUpdate", (com) => { debouncedAutoSave();   });
  editor.on("property:compAdd:", (com) => { debouncedAutoSave();   });
  editor.on("property:compRemove:", (com) => { debouncedAutoSave();   });
  editor.on("property:compUpdate:", (com) => { debouncedAutoSave();   });

  let debouncedAutoSave = _.debounce(function () {
    //console.log( '>>>>>>>>>> component:autoSave' );
      emit("autoSaveTriggered");
  }, 10000);



  editor.on("canvas:dragdata", (dataTransfer, result, event) => {
    // If dataTransfer is present, it's a binding
    if( dataTransfer ){
      let objectData = dataTransfer.getData("text");
      objectData = JSON.parse(objectData);
      if (objectData.objectType == "binding") {
        result.content = `<tempplaceholder style="padding:10px;" bindingname="${objectData.bindingname}" datatype="${objectData.datatype}">{{ ${objectData.bindingname} }}</tempplaceholder>`;
      }
      // Else, it's a block from the custom-native library
    }else{
      result.content = result.content.content
    }
  });

  // ---------- GENIE/STIPPLE COMPONENTS ----------
  editor.DomComponents.addType('julia_code', {
    model:{
      defaults: {
        tagName: 'julia_code',
          editable: true,
          droppable: false,
      }
    }, 
    view: {
		events: {
			dblclick: 'onActive',
			focusout: 'onDisable',
		},
		onActive() {
			this.el.contentEditable = true;
		},
		onDisable() {
			const { el, model } = this;
			el.contentEditable = false;
			model.set('content', el.innerHTML)
		},
	}
  });

  // ---------- SPECIAL TEMPLATE COMPONENT ----------
  editor.DomComponents.addType('template', {
    isComponent: function(el) {
      if( el.tagName == 'TEMPLATE' ){
        return { type: 'template', name: 'Template', components:el.innerHTML };
      };
    },
    model: {
      defaults: {
        tagName: 'template',
        droppable: true
      }
    }, 
    view: {
      onRender() {
        const { el, model } = this;
      }
    }
        
  });

  // ---------- VUE CUSTOM COMPONENTS ----------
  registerPlotlyComponent(editor);

  // ---------- ROW COMPONENT ----------
  editor.DomComponents.addType('row-div', {
    isComponent: el => {
      return el.tagName === 'DIV' && el.classList.contains('row');
    },
    model: {
      defaults: {
        tagName: 'div',
        classes: ['row'],
        droppable: function(el) {
          // Prevent dropping if the parent is also a 'row' div
          return !el.view.$el[0].classList.contains('row');
        }
      }
    },
    // Optionally, customize the view
    view: {}
  });

  // ---------- CUSTOM COMPONENTS ----------
  /* editor.DomComponents.addType( "looper", {
    isComponent: (el) => {
      // match element has attribute "looper"
      if (el.tagName == "DIV" && el.hasAttribute("looper") ) {
        return { type: "looper", name: "Looper" };
      }
    },
    model: {
      defaults: {
        draggable: true,
        droppable: true,
        editable: true,
        isComposite: false,        
      },
    }
  }); */
  
  blockDefinitions.forEach((definition) => {
    processBlockDefinition(definition);
  });

  // Load autogenerated styles
  let styles = props.appData.pages[0].assets;
  let autogeneratedStyles = styles.find((style) => {
    return style.indexOf('autogenerated.css') > -1;
  });
  if( autogeneratedStyles ){
    let stylePath = window.apibase + 'apps/' + Configuration.appid + '/edit?path=' + encodeURIComponent( autogeneratedStyles );
    axios.get( stylePath ).then( (result) => {
      editor.setComponents(mockedAppTemplate.value);
      editor.setStyle(result.data.content);
    }).catch( (error) => {
      console.log('error loading autogenerated styles', error);
    });
  }else{
    editor.setComponents(mockedAppTemplate.value);
  }

}

function removeVueInternalDivs(htmlString) {
    // Create a DOM parser to parse the HTML string
    const parser = new DOMParser();
    const doc = parser.parseFromString(htmlString, 'text/html');

    // Find all div elements in the document
    const divs = doc.querySelectorAll('div[id^="vueinternal_"]');

    // Iterate over each div with an id starting with 'vueinternal_'
    divs.forEach(div => {
        // Insert all child nodes of the div (including text nodes) before the div itself
        while (div.firstChild) {
            div.parentNode.insertBefore(div.firstChild, div);
        }
        // Remove the empty div
        div.parentNode.removeChild(div);
    });

    // Return the modified HTML as a string
    return doc.body.innerHTML;
}

function processBlockDefinition( definition){
  const blacklist = ["plotly", "tempplaceholder"];
    if (blacklist.includes(definition.type)) {
      return;
    }
    const nativeElements = ["H1", "H2", "H3", "H4", "H5", "H6", "P", "SPAN", "A"];
    //const nativeElements = ["div", "span", "h1", "h2", "h3", "h4", "h5", "h6", "p", "a", "img", "ul", "ol", "li", "table", "tr", "td", "th", "thead", "tbody", "tfoot", "caption", "col", "colgroup", "thead", "tfoot", "tbody", "caption", "col", "colgroup", "thead", "tfoot", "tbody", "caption", "col", "colgroup", "thead", "tfoot", "tbody", "caption", "col", "colgroup", "thead", "tfoot", "tbody", "caption", "col", "colgroup", "thead", "tfoot", "tbody", "caption", "col", "colgroup", "thead", "tfoot", "tbody", "caption", "col", "colgroup", "thead", "tfoot", "tbody", "caption", "col", "colgroup", "thead", "tfoot", "tbody", "caption", "col", "colgroup"];
    if( nativeElements.includes(definition.tagName) ){
      

    }else if( definition.attributeMatch != undefined || nativeElements.includes(definition.type) ){
      //console.log( 'definition.attributeMatch: ', definition.type );
      editor.DomComponents.addType(definition.type, {
        isComponent: (el) => {
          if (el.tagName == definition.tagName && el.hasAttribute(definition.attributeMatch ) ) {
            return { type: definition.type, name: definition.label };
          }
        },
        model: {
          defaults: {
            draggable: definition.draggable,
            droppable: definition.droppable,
            editable: definition.editable,
            isComposite: definition.isComposite,
            
          },
        }
      });

    /* }else if(definition.type=='q-tabs'){      
      editor.DomComponents.addType(definition.type, {
        isComponent: (el) => {
          if (el.tagName == definition.tagName) {
            return { type: definition.type, name: definition.label };
          }
        },
        model: {
          defaults: {
            draggable: definition.draggable,
            droppable: definition.droppable,
            editable: definition.editable,
            isComposite: definition.isComposite,
            
          },
        }
      }); */

    }else{      
      editor.DomComponents.addType(definition.type, {
        isComponent: (el) => {
          if (el.tagName == definition.tagName) {
            return { type: definition.type, name: definition.label };
          }
        },
        model: {
          defaults: {
            //traits: definition.traits,
            draggable: definition.draggable,
            droppable: definition.droppable,
            editable: definition.editable,
            isComposite: definition.isComposite,
            script: function(){    
              var serverData = window.parent.sharedData.externalData;
              
              let uniqueId = this.attributes.id.nodeValue;

              window.parent.addCanvasComponent( uniqueId, this );
              
              function getMappedAttributes(attributesIn){
                let mappedAttributes = {};                    
                let attributeNames = Object.getOwnPropertyNames(attributesIn);
                attributeNames.forEach( (attrName)=>{
                  let attributeValue = attributesIn[attrName] !== undefined ? attributesIn[attrName]: attributesIn[attrName];
                  mappedAttributes[attrName] = attributeValue;
                });
                return mappedAttributes;
              }

              if( !this.grapesModel ){
                return;
              }
              
              let mappedAttributes = getMappedAttributes(this.grapesModel.attributes.attributes);

              this.onExternalDataChanged = (event)=>{
                  serverData = event.detail;
                  mappedAttributes = getMappedAttributes(this.attributes);
                  this.vueApp.updateServerData(serverData);
              }
              
              let tagName = this.grapesModel.attributes.tagName;
              let attributesObject = this.grapesModel.attributes.attributes;
              let getComponentDefinitionByType = window.parent.sharedData.getComponentDefinitionByType;
              let modelFieldsByName = window.parent.sharedData.modelFieldsByName;
              let componentDefinition = getComponentDefinitionByType( tagName );

                Vue.config.warnHandler = (err, vm, info) => {
                  let element = document.getElementById(uniqueId);
                  element.innerHTML = `<div class="componentError"><b style="font-size: 1.2em;">${componentDefinition.label}</b><br/>${err}</div>`;
                  // err: error trace
                  // vm: component in which error occured
                  // info: Vue specific error information such as lifecycle hooks, events etc.
                };
                Stipple.init({theme: 'stipple-blue'});
              this.vueApp = new Vue({
                  el: '#vueinternal_'+uniqueId,
                  //components: { QBtn:Quasar.}, 
                  data: function(){ 
                    let result = serverData;
                    result.attributes = mappedAttributes;
                    result.serverData = serverData;
                    return result;
                  }, 
                  computed:{
                    parsedAttributes(){
                      let result = {};
                      let attributesObject = this.attributes;
                      for( let keyRaw in attributesObject ){
                        let key = keyRaw[0] == ':' ? keyRaw.substring(1) : keyRaw;
                        let propertyDefinition = componentDefinition.properties.find( prop => prop.name == key );
                        let attributeValue = attributesObject[keyRaw];
                        let valueIsBinding = modelFieldsByName[attributeValue] != null;
                        if( key == 'v-model' ){
                          result['v-model'] = this.serverData[attributeValue];
                          continue;
                        }
                        if( propertyDefinition ){
                          if( valueIsBinding ){
                            result[`:${key}`] = this.serverData[attributeValue];
                          }else{
                            if( propertyDefinition.type == "String" ){
                              result[`${key}`] = attributeValue;
                            }else{
                              result[`:${key}`] = attributeValue;
                            }
                          }
                        }else{
                          result[`${key}`] = attributeValue;
                        }
                      }
                      return result;
                    }
                  },
                  methods: {
                      updateServerData(newServerData){
                          this.serverData = newServerData;
                      }, 
                      updateAttributes(attrs){             
                          this.attributes = getMappedAttributes(attrs);
                          mappedAttributes = this.attributes;
                          this.updateServerData(this.serverData);
                          this.$forceUpdate();
                      }, 

                  }, 
                  mounted() {
                  }
              });

            }
          },
          init() {
            this.listenTo(this, 'change:attributes', this.onAttributeChange);
          },
          handleAttrChange() {
            //this.render();
          },
          render: function () {
            this.view.onRender();
          },
          removed() {
              window.parent.postMessage( {command:"removeCanvasComponent", uid:this.ccid }, "*" );
          },
        },
        view: {
          onAttributeChange() {
          }, 
          onRender() {
            const { el, model } = this;
            let uniqueId = el.attributes.id.nodeValue;
            el.grapesModel = model;

            let tagName = el.grapesModel.attributes.tagName;

            let attributesObject = el.grapesModel.attributes.attributes;
            let componentDefinition = getComponentDefinitionByType( tagName );
            let renderContent = `<div id="vueinternal_${uniqueId}" style="position: relative;">
              <${tagName} `;
              
              for( let keyRaw in attributesObject ){
                let key = keyRaw;
                if( key.indexOf('gb_c__') == 0 || key.indexOf('gb_b__') == 0 ){
                  key = key.substring(6);
                }              
                //= keyRaw[0] == ':' ? keyRaw.substring(1) : keyRaw;
                let propertyDefinition = componentDefinition.properties.find( prop => prop.name == key );
                let attributeValue = attributesObject[keyRaw];
                let valueIsBinding = modelFieldsByName[attributeValue] != null;
                
                // If binding not recognised, check if it's a composed name (binding.property)
                if( !valueIsBinding && attributeValue.indexOf('.') > -1 ){
                  let bindingParts = attributeValue.split('.');
                  let bindingName = bindingParts[0];
                  // Check if binding exists with that name and if it's a dataTable
                  let bindingObject = modelFieldsByName[bindingName];
                  if( bindingObject && bindingObject.type == 'DataTable.DataFrames.DataFrame' ){
                    // then check among its props
                    if( bindingObject.props && bindingObject.props.includes( bindingParts[1] ) )
                      valueIsBinding = true;
                  }
                }
                if( key == 'v-model' ){
                  if( attributeValue == '' )
                    renderContent += `v-model="serverData.none" `;
                  else
                    renderContent += `v-model="serverData.${attributeValue}" `;
                  continue;
                }
                if( propertyDefinition ){
                  if( valueIsBinding ){
                    renderContent += `:${key}="serverData.${attributeValue}" `;
                  }else{
                    // Special case for images, so that their path is relative to the app
                    if( componentDefinition.type == 'q-img' && key == 'src' && propertyDefinition.type == "String" && !attributeValue.startsWith('http') ){
                      if( attributeValue.startsWith('/') )
                        attributeValue = appBasePath + attributeValue;
                      else
                        attributeValue = appBasePath + '/' + attributeValue;
                    }
                    if( key=='v-if' || key=='v-for'){
                      // skip
                    }else if( propertyDefinition.type == "String" || key.indexOf('v-on') == 0 ){
                      renderContent += `${key}="${attributeValue}" `;
                    }else{
                      renderContent += `:${key}="${attributeValue}" `;
                    }
                  }
                }else{
                  renderContent += `${key}="${attributeValue}" `;
                }
              }

              renderContent += `>`;
              
              let isNestable = getComponentDefinitionByType( tagName )?.nestable;
              if( isNestable ){
                // some nestable components have trouble rendering their children in the editor,
                // so here we make sure that the inner html is not altered by grapes and vue can render as normal
                if( tagName == "q-tab-panels" || tagName == "q-tab-panel" || tagName == "q-card" || tagName == "q-card-section" ){
                  let cleanedHtml = model.getInnerHTML();

                  const parser = new DOMParser();
                  const doc = parser.parseFromString(cleanedHtml, 'text/html');
                  const firstDiv = doc.body;
                  

                  HtmlTemplateParser.decodeAttributesInDOM(firstDiv );
                  renderContent += firstDiv.innerHTML;
                  }else{
                    // for all other nestable components, we just do some cleanup before appending children
                    let innerHtmlClean = el.innerHTML;
                    if( innerHtmlClean != '' ){
                      const parser = new DOMParser();
                      const doc = parser.parseFromString(innerHtmlClean, 'text/html');
                      const firstDiv = doc.body;//.querySelector('#itkg') || null;  
                      firstDiv.children.forEach( (child)=>{
                        HtmlTemplateParser.decodeAttributesInDOM(child );
                        let inner = child.innerHTML;
                        renderContent += inner;
                      });
                    }
                }
              }

              renderContent += `</${tagName}>
              </div>
            `;

            
            el.innerHTML = renderContent;
          },
        },
      });

    }

  // Load autogenerated styles
  /* let styles = props.appData.pages[Configuration.currentPageIndex].assets;
  let autogeneratedStyles = styles.find((style) => {
    return style.indexOf('autogenerated.css') > -1;
  });
  if( autogeneratedStyles ){
    let stylePath = window.apibase + 'apps/' + Configuration.appid + '/edit?path=' + encodeURIComponent( autogeneratedStyles );
    axios.get( stylePath ).then( (result) => {
      editor.setComponents(mockedAppTemplate.value);
      editor.setStyle(result.data.content);
    }).catch( (error) => {
      console.log('error loading autogenerated styles', error);
    });
  }else{
    editor.setComponents(mockedAppTemplate.value);
    } */

}

function showAiCodePreview(status){
  aiCodePreviewShown.value = status;
}

function addShakeEffect($event) {
  if( $event.target.id != 'canvasBlocker' )
    return;

  const element = document.getElementById('canvasBlockerDialog');
  element.classList.add('shake-effect');

  // Optional: Remove the class after the animation ends to reapply it later
  element.addEventListener('animationend', () => {
    element.classList.remove('shake-effect');
  }, {once: true});
}

function updateReactiveModel(newModel){
    reactiveModel.value = newModel;
}


function updateTemplateFile(fileContents){

  // Clean DIVS (remove empty text nodes) to fix inability to drop components inside containers
  fileContents = HtmlTemplateParser.removeInvalidNodes(fileContents);

  fileContents = HtmlTemplateParser.replaceSpecialElements( fileContents );
    
    let fullContent = `
    <div class="container layoutMockRoot" data-gjs-removable="false" data-gjs-draggable="false" data-gjs-droppable="false">
      <div class="row layoutMockRow" data-gjs-removable="false" data-gjs-draggable="false" data-gjs-droppable="false">
        <div class="col-12 layoutMockCol" data-gjs-removable="false" data-gjs-draggable="false" data-gjs-droppable="false">
          <div id="gbTemplateContainer" class="container" data-gjs-removable="false" data-gjs-draggable="false" data-gjs-type="default">
            ${fileContents}
          </div>
        </div>
      </div>
    </div>`;

    // Remove colons from attributes as needed
    const parser = new DOMParser();
    const doc = parser.parseFromString(fullContent, 'text/html');
    const firstDiv = doc.body;

    HtmlTemplateParser.encodeAttributesInDOM(firstDiv );
    
    
    const firstDivContent = firstDiv.innerHTML;

    mockedAppTemplate.value = firstDivContent;
    initMockedGenieApp();
}

let dragStart;



onMounted(() => {
});

defineExpose({
  updateReactiveModel, updateTemplateFile, showAiCodePreview
}) 
</script>

